$navbar-inverse-border: #0e4770;
$brand-primary: #3498db;
$brand-warning: #e67e22;
$brand-danger: #d43f3a;
$title-blue: #356b91;
$extra-light-gray: #eee;
$light-gray: #e0e0e0;
$gray: #ababab;
$dark-gray: #9A9A9A;

$brand-darkblue: #1d2d47;
$brand-orange: #f26649;
$brand-turqoise: #3fc0c2;
$brand-blue: #0072bc;
$brand-yellow: #ffcb05;
$brand-mint: #c9dccf;
$brand-mint-transparent: rgba(201, 220, 207, 0.5);
$sand-transparent: rgba(235, 220, 184, 0.5);
$text-turqoise: #149b9e;
$text-gray: #777777;
$text-darkblue: #183058;
$text-font: roboto, arial, sans-serif;
$gray: #999;
$light-gray: #ddd;
$light-mint: #dfe8e2;

$grid-gutter-width: 0;


@font-face {
  font-family: Dense;
  src: url("fonts/Dense-Regular.otf") format("opentype");
}

@mixin headline-font {
  font-family: Dense, arial, sans-serif;
  letter-spacing: 1.5px;
}

@mixin button {
  @include headline-font;
  letter-spacing: 1.5px;
  font-size: 24px;
  border: 1px solid #183058;
  text-align: center;
  width: 120px;
  margin-right: 15px;
  margin-top: 15px;
  cursor: pointer;
  height: 34px;
  line-height: 1;
  padding-top: 5px;
  &:focus {
    text-decoration: none;
  }
}

@mixin bottom-shadow {
  -webkit-box-shadow: 0px 5px 5px 0px rgba(119, 119, 119, 0.14);
  -moz-box-shadow: 0px 5px 5px 0px rgba(119, 119, 119, 0.14);
  box-shadow: 0px 5px 5px 0px rgba(119, 119, 119, 0.14);
}

.ember-application {
  font-family: $text-font;
  font-weight: 300 !important;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 300 !important;
}

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

body > .ember-view {
  height: 100%;
}

@each $name, $color, $alt-color in (button-white, $brand-darkblue, none),
                                   (button-orange, white, $brand-orange)
{
  .#{$name} {
    @include headline-font;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 130px;
    height: 32px !important;
    padding: 2px 10px;
    font-size: 18px;
    letter-spacing: 2px;
    color: #{$color};
    border: 1px solid #{$color} !important;
    background: $alt-color;
    border-radius: 0 !important;
  }
}

@each $name, $color, $background, $textcolor in
                       (orange-button, $brand-orange, transparent, $brand-orange),
                       (solid-orange-button, $brand-orange, $brand-orange, white),
                       (blue-button, $brand-darkblue, transparent, $brand-darkblue)
{
  .#{$name} {
    @include headline-font;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    height: 35px !important;
    padding: 4px 30px;
    font-size: 24px;
    letter-spacing: 2px;
    color: #{$textcolor};
    border: 1px solid #{$color} !important;
    border-radius: 0 !important;
    cursor: pointer;
    background-color: $background;
    text-transform: uppercase;
    line-height: 1;
    &:hover {
      background-color: $color;
      color: white !important;
    }
    &:visited, &:active, &:focus {
      color: #{$color};
    }
    &:hover, &:visited, &:active, &:focus {
      text-decoration: none;
    }
    @media (max-width: $screen-xs-max) {
      height: 32px !important;
    }
  }
}
.turquoise-button {
  color: white;
  background: #3fc0c2;
  font-family: roboto, arial, sans-serif;
  font-size: 15px;
  font-weight: 400;
  height: 50px;
  min-width: 180px;
  width: fit-content;

  padding-left: 30px;
  padding-right: 30px;
  text-align: center;

  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #3fc0c2;
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.16);
  border-radius: 3px;
  letter-spacing: 1.5px;
  @media (max-width: $screen-xs-max) {
    height: 45px;
    width: 160px;
    padding-left: 10px;
    padding-right: 10px;
  }

  &:hover, &:visited, &:focus {
    background: #3fc0c2;
    color: white;
    text-decoration: none;
  }
  &:hover {
    background: #149B9E;
    border: 1px solid #149B9E;
  }
}

.white-button {
  cursor: pointer;
  color: #149B9E;
  background: #fff;
  font-family: roboto, arial, sans-serif;
  font-size: 15px;
  font-weight: 400;
  height: 50px;
  min-width: 180px;
  width: fit-content;

  padding-left: 30px;
  padding-right: 30px;
  text-align: center;

  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #3FC0C2;
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.16);
  border-radius: 3px;
  letter-spacing: 1.5px;

  @media (max-width: $screen-xs-max) {
    height: 30px;
    width: 140px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 13px;
  }

  &:hover, &:focus {
    background: #3fc0c2;
    color: white;
    text-decoration: none;
  }
  &[disabled] {
      background: #C5C9D0;
      border: 1px solid #C5C9D0;
      color: white;
      cursor: not-allowed;
    }
}

.darkblue-button {
  height: 50px;
  padding-left: 20px;
  padding-right: 20px;
  color: white;
  font-size: 16px;
  background: #1F2B45;
  font-family: roboto, arial, sans-serif;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #1F2B45;
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.16);
  border-radius: 3px;
  letter-spacing: 1.5px;
  min-width: 181px;
  width: fit-content;
  margin-top: 35px;
  font-weight: 400;
  margin-bottom: 20px;

  &:hover, &:visited, &:focus {
    background: #1F2B45;
    color: white;
    text-decoration: none;
  }
}

