.bulk-upload {
  width: 800px;
  text-align: left;
  font-size: 16px;
  line-height: 24px;
  p {
    font-size: 16px;
  }
  h3 {
    color: #1d2d47;
  }
  .buttons {
    display: flex;
    align-items: flex-start;
    flex-basis: 50%;
    justify-content: flex-start;
    padding-top: 25px;
  }
  .button {
    letter-spacing: 1.5px;
    border: 1px solid #183058;
    text-align: center;
    margin-top: 15px;
    cursor: pointer;
    line-height: 2;
    border-color: #f26649;
    background-color: #f26649;
    color: white;
    text-decoration: none;
    display: flex;
    justify-content: center;
    font-family: roboto;
    font-size: 15px;
    padding-top: 0;
    align-items: center;
    height: 40px;
    width: 165px;
    box-shadow: 1px 2px 1px rgba(0,0,0,0.2);
    &.cancel {
      color: #f26649;
      background-color: white;
      border-color: #f26649;
      margin-right: 15px;
      line-height: 2.6;
    }
    &.invisible {
      visibility: hidden;
    }
  }
  .auto-submit {
    padding-top: 20px;
  }
  .auto-submit-checkbox {
    display: flex;
    align-items: center;
    input {
      border-radius: 3px;
    }
    input[type="checkbox"] {
      -webkit-appearance: none;
      -moz-appearance: none;
      -ms-appearance: none;
      -o-appearance: none;
      appearance: none;
      position: relative;
      right: 0;
      bottom: 0;
      left: 0;
      height: 20px;
      width: 20px;
      transition: all 0.15s ease-out 0s;
      background: white;
      border-radius: 50%;
      border: none;
      color: #fff;
      cursor: pointer;
      display: inline-block;
      margin-right: 0.5rem;
      outline: none;
      position: relative;
      z-index: 80;
    }
    input[type="checkbox"]::before {
      height: 20px;
      width: 20px;
      position: absolute;
      content: '';
      display: inline-block;
      font-size: 10.66667px;
      text-align: center;
      line-height: 15px;
      border: 1px solid #8D95A2;

      // border: 1px solid #3FC0C2;
      border-radius: 50%;
    }
    input[type="checkbox"]:checked {
      background: #3FC0C2;
      // border: 1px solid #3FC0C2;
    }
    input[type="checkbox"]:checked::before {
      height: 20px;
      width: 20px;
      position: absolute;
      content: '';
      display: inline-block;
      font-size: 10.66667px;
      text-align: center;
      line-height: 20px;
      border: 1px solid #3FC0C2;
    }
    input[type="checkbox"]:checked::after {
      -webkit-animation: click-wave 0.65s;
      -moz-animation: click-wave 0.65s;
      animation: click-wave 0.65s;
      background: #3FC0C2;
      content: '';
      display: block;
      position: relative;
      z-index: 100;
    }

    input[type="checkbox"].radio {
      border-radius: 50%;
    }
    input[type="checkbox"].radio::after {
      border-radius: 50%;
    }
  }

  .selected-images {
    padding-top: 15px;
  }
  .shots {
    width: 400px;
    color: #1d2d47;
  }
  .bulk-uploading {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .title {
      font-size: 22px;
    }
    .progress-container {
      display: flex;
      flex-direction: column;
      .title {
        font-size: 22px;
        padding-bottom: 10px;
      }
      .content {
        padding-bottom: 15px;
        font-size: 18px;
      }
    }
    .loader-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .uploadProgress {
        font-size: 55px;
        color: $brand-turqoise;
        @include headline-font;
        position: relative;
        top: -105px;
      }
      .loader,
      .loader:after {
        border-radius: 50%;
        width: 185px;
        height: 185px;
      }
      .loader {
        font-size: 10px;
        position: relative;
        text-indent: -9999em;
        border-top: 1.1em solid rgba(63,192,194, 0.2);
        border-right: 1.1em solid rgba(63,192,194, 0.2);
        border-bottom: 1.1em solid rgba(63,192,194, 0.2);
        border-left: 1.1em solid rgba(63,192,194, 1);
        -webkit-transform: translateZ(0);
        -ms-transform: translateZ(0);
        transform: translateZ(0);
        -webkit-animation: load8 1.1s infinite linear;
        animation: load8 1.1s infinite linear;
      }
      @-webkit-keyframes load8 {
        0% {
          -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
        }
        100% {
          -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
        }
      }
      @keyframes load8 {
        0% {
          -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
        }
        100% {
          -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
        }
      }
    }
  }
}
