.getty-mission-header {
  height: auto;
  padding: 0;
  margin: 0;

  height: 47vw;
  text-align: center;

  color: white;
  font-size: 32px;
  img {
    width: 188px;
  }

  @media (max-width: $screen-xs-max) {
    height: 113vw;
    h1 {
      font-size: 25px;
    }
  }
  .getty-video-background {
    z-index: -100;
    width: 100%;
    position: absolute;
    left: 0;
  }
  .header-text {
    z-index: 100;
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    p {
      font-size: 18px;
      width: 381px;
      padding-bottom: 20px;
      @media (max-width: $screen-xs-max) {
        width: 303px;
        font-size: 14px;
      }
    }
    img {
      @media (max-width: $screen-xs-max) {
        width: 170px;
      }

      width: 200px;
    }
  }
}
.getty-mission {
  .headline {
    font-family: roboto, arial, sans-serif;
    font-size: 30px;
  }
  overflow: hidden;
  color: $brand-darkblue;
  font-size: 15px;
  .scroll-hidden {
    visibility: hidden;
  }
  .get-on {
    height: 285px;
    padding-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 75px;
    position: relative;
    overflow: hidden;
    @media (max-width: $screen-xs-max) {
      height: auto;
      padding-top: 35px;
      background-image: none;
      padding-bottom: 60px;
      h1 {
        font-size: 25px;
      }
    }
    .cta {
      margin-top: 40px;
    }
    .lowertri {
      width: 100%;
      top: 0;
      left: 0;
      position: absolute;
      z-index: -1;
    }
    .getty-lockup {
      width: 210px;
      margin: 30px 0 20px;
    }
    .body {
      display: flex;
      flex-direction: row;
      max-width: 757px;
      text-align: center;
      font-weight: lighter;
      @media (max-width: $screen-xs-max) {
        padding: 0 35px;
        font-size: 15px;
        text-align: center;
        flex-direction: column;
        align-items: center;
      }
    }
  }
  .infog {
    background-image: url("/assets/images/getty/Infographic_Steps_bg.png");
    background-repeat: no-repeat;
    background-position: top left;
    background-size: cover;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    .title {
      font-size: 30px;
      @media (max-width: $screen-xs-max) {
        font-size: 25px;
      }
      margin-top: 50px;
    }
    .info-gifs {
      position: relative;
      .hover {
        position: absolute;
        visibility: hidden;
        top: 0;
        left: 0;
      }
      &:hover {
        .hover {
          visibility: visible;
        }
        .nohover {
          visibility: hidden;
        }
      }
    }
    .infog-row {
      display: flex;
      flex-direction: row;
      width: 100%;

      .infog-column {
        padding-bottom: 5%;
        width: 33%;

        .info-gifs {
          /*padding-left: 10%;
          padding-right: 10%;*/
        }

        @media (max-width: $screen-xs-max) {
          width: 100%;
        }

        .overlay1 img {
          width: 200px;
        }

        .overlay2 img, .overlay3 img {
          width: 190px;
        }

        img {
          display: block;
          margin-left: auto;
          margin-right: auto;
          @media (max-width: $screen-xs-max) {
            padding: 0;
          }
        }

        .text {
          width: 250px;
          text-align: center;
          margin: auto;
        }
      }
    }
    @media (max-width: $screen-xs-max) {
      background-image: none;
      padding-bottom: 60px;
      background-color: #f6f6f6;
      .title {
        margin-top: 40px;
        margin-bottom: 0px;
        text-align: center;
      }
      .infog-row {
        flex-direction: column;
        text-align: center;
      }
    }
  }

  .video-guidelines {
    display: flex;
    flex-direction: row;
    width: 100%;
    @media (max-width: 1100px) {
      flex-direction: column;
    }
  }

  .example-video-row {
    width: 59%;
    padding-top: 40px;
    @media (max-width: 1100px) {
      width: 100%;
    }
    .mission-examples {
      width: 580px;

      @media (max-width: 1100px) {
        width: 100%;
        padding-top: 0;
      }
      @media (max-width: 760px) {
        width: 320px;
      }
    }
    .example-video-headline {
      text-align: center;
      font-size: 20px;
      margin-bottom: 20px;
      margin-top: 20px;
      @media (max-width: $screen-xs-max) {
        margin-left: 0px;
      }
    }
  }
  .guidelines {
    width: 40%;
    padding: 60px;
    padding-top: 40px;

    @media (max-width: 1100px) {
      width: 100%;
      padding: 20px;
      margin: 0;
    }

    .headline {
      padding: 20px;
    }
    @media (max-width: $screen-xs-max) {
      .headline {
        text-align: center;
        margin-left: 0;
        margin-bottom: 20px;
      }
      .example-video-headline {
        text-align: center;
        margin-top: 30px;
      }
      .col.example-video-column {
        padding: 0;
      }
      .bullets {
        width: 100%;
      }
    }

    .items {
      margin-bottom: 0;
      max-width: 540px;
      li {
        padding-left: 5px;
        padding-bottom: 12px;
      }
    }
    @media (max-width: $screen-xs-max) {
      .items {
        padding: 0 5px 0 30px;
        line-height: 1.7;
      }
      li {
        padding-bottom: 15px;
      }
    }
    a {
      color: $brand-turqoise;
      margin-left: 45px;
      font-style: italic;
      @media (max-width: $screen-xs-max) {
        margin-left: 35px;
      }
    }
  }

  .cols {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding-top: 60px;
    .col {
      width: 50%;
    }

    @media (max-width: $screen-xs-max) {
      flex-direction: column;
      .col {
        width: auto;
        padding: 0 20px;
      }
    }
  }
  .upload {
    .mobile-upload-redirect {
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      font-family: roboto, arial, sans-serif;
      font-weight: lighter;
      div {
        font-size: 15px;
        max-width: 320px;
      }
      img {
        max-width: 200px;
        margin: 20px;
        max-height: 200px;
      }
      .log-in-text {
        padding-top: 40px;
        padding-bottom: 0px;
        margin-top: 20px;
      }
      .use-app-text {
        margin-bottom: 10px;
      }
      .app-store-badge {
        width: 200px;
      }
      .more-questions-text {
        margin-top: 60px;
        margin-bottom: 30px;
      }
      .pilot-faq-button {
        width: 170px;
        margin-top: 5px;
        margin-bottom: 150px;
      }
      .large-turquoise-text {
        color: $brand-turqoise;
        font-size: 20px;
        margin: 20px;
      }
    }
    .nice-select {
      margin-top: 5px;
      border-radius: 0;
      border: 1px solid #ccc;
    }
    .nice-select:after {
      height: 10px;
      width: 10px;
      margin-top: -6px;
    }
    .nice-select .list {
      display: none;
    }
    .nice-select.open .list {
      li.option.disabled {
        display: none;
      }
      border-radius: 0;
      margin-top: 1px;
      display: block;
    }
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f6f6f6;
    background-image: url("/assets/images/getty/Upload_background_updated.png");
    background-repeat: repeat-x;
    background-position: left center;
    overflow: hidden;
    @media (max-width: $screen-xs-max) {
      background-repeat: repeat-x;
      background-position: left 20px;
      background-image: url("/assets/images/getty/Upload_Background_header_mobile_1.png");
    }
    .headline {
      margin-top: 50px;
      margin-bottom: 20px;
      @media (max-width: $screen-xs-max) {
        padding-bottom: 40px;
      }
    }
    .upload-panel {
      background-color: white;
      padding: 40px;
      width: 600px;
      color: $brand-darkblue;
      -webkit-box-shadow: 0px 0px 16px 0px rgba(0,0,0,0.25);
      -moz-box-shadow: 0px 0px 16px 0px rgba(0,0,0,0.25);
      margin-bottom: 200px;
      box-shadow: 0px 0px 16px 0px rgba(0,0,0,0.25);
      @media (max-width: $screen-xs-max) {
        width: auto;
        background-color: transparent;
        color: $brand-darkblue;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        &.upload-complete {
          background-color: white;
        }
      }
    }
    .uploader-container {
      width: 100%;
    }
    .select-file-panel {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    .textbox {
      padding: 5px 10px;
      border: 2px solid #eee;
      width: 100%;
    }
    .description {
      width: 80%;
    }
    textarea.textbox {
      padding: 10px;
    }
    ::-webkit-input-placeholder { /* Chrome/Opera/Safari */
      color: #aaa;
    }
    ::-moz-placeholder { /* Firefox 19+ */
      color: #aaa;
    }
    :-ms-input-placeholder { /* IE 10+ */
      color: #aaa;
    }
    :-moz-placeholder { /* Firefox 18- */
      color: #aaa;
    }
    .emph {
      font-weight: 500;
    }
    .optional {
      font-style: italic;
      color: #777;
    }
    .title {
      color: $brand-orange;
      font-size: 20px;
      font-weight: 400;
    }
    .upload-button {
      margin: 20px 0;
    }
    .input-row {
      display: flex;
      flex-direction: row;
      input {
        width: 100%;
      }
      @media (max-width: $screen-xs-max) {
        flex-direction: column;
        width: 100%;
      }
    }
    .upload-form-section {
      margin-top: 35px;
    }
    .categories-dropdown {
      margin-top: 10px;
      margin-bottom: 10px;
      width: 100%;
      border-color: #eee;
      border-radius: 0;
      border-width: 2px;
      padding-left: 10px;
      font-family: roboto, arial, sans-serif;
      font-size: 15px;
      font-weight: 300;
      color: #1d2d47;
      .list {
        width: 100%;
        border-radius: 0;
        .option {
          padding-left: 10px;
        }
      }
    }
    .categories {
      width: 100%;
    }
    .no-category {
      .current {
        color: #999;
      }
    }
    .categories-error {
      .categories-dropdown {
        border-color: red;
        color: #999;
      }
    }

    textarea {
      resize: none;
      width: 100%;
      margin: 10px 0 10px 0;
      border-color: #ddd;
      height: 100px;
    }
    input {
      margin: 10px 0 10px 0;
      height: 40px;
      border: 2px solid #eee;
      padding: 0 10px;
    }
    .left {
      @media (min-width: $screen-xs-max) {
        margin-right: 10px;
      }
    }
    .video-thumbnail {
      width: 100%;
      @media (min-width: $screen-xs-max) {
        width: 33%;
      }
    }
    .upload-form {
      display: flex;
      flex-direction: column;
    }
    .submit-button {
      align-self: center;
      margin-top: 30px;
      font-size: 23px;
      width: 212px;
      img {
        width: 100%;
      }
    }
    .ig3, .ig2 {
      width: 140px;
    }
  }
  .textbox-error {
    color: $brand-orange;
    font-size: 12px;
  }
  .error-text {
    color: $brand-orange;
    text-align: center;
    padding-top: 30px;
    @media (max-width: $screen-xs-max) {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      background-color: $brand-orange;
      color: white;
      padding: 10px;
    }
  }
  #fileInput {
    // File input is hidden, so we can style the label instead
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }
  .filenameWrapper {
    margin: 20px 0 0px 0;
  }
  .cancel {
    font-size: 15px;
    color: grey;
    margin-left: 20px;
    cursor: pointer;
  }
  .uploadFilename {
    font-size: 18px;
    font-weight: 400;
  }
  #fileInputLabel {
    width: 100%;
    font-size: 18px;
    font-weight: 400;
    cursor: pointer;
    padding: 5px 10px;
    border: 2px solid transparent;
    img {
      width: 25px;
      margin-top: -6px;
      margin-right: 10px;
    }
  }
  .uploadfilesize {
    font-size: 13px;
    color: #999;
  }
  .error {
    border: 2px solid $brand-orange !important;
  }

  .progress-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 670px;
    .headline {
      color: $brand-orange;
      font-size: 40px;
      font-family: Dense;
      letter-spacing: 2.5px;
    }
    .uploadProgress {
      font-size: 55px;
      color: $brand-turqoise;
      @include headline-font;
      position: relative;
      top: -128px;
    }
    .cancel-button {
      margin-top: 30px;
      width: 212px;
    }
  }

  .complete-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 670px;
    text-align: center;
    position: relative;
    .close {
      position: absolute;
      top: -20px;
      right: -20px;
    }
    .headline {
      color: $brand-orange;
      padding: 5px 0;
      font-size: 40px;
      letter-spacing: 2.5px;
      font-family: Dense;
      padding-bottom: 10px;
      margin-bottom: 0;
    }
    img {
      width: 300px;
    }
    .text {
      max-width: 400px;
    }
    .submit-again {
      margin-top: 30px;
      width: 278px;
    }
  }

  .loader,
  .loader:after {
    border-radius: 50%;
    width: 185px;
    height: 185px;
  }
  .loader {
    font-size: 10px;
    position: relative;
    text-indent: -9999em;
    border-top: 1.1em solid rgba(63,192,194, 0.2);
    border-right: 1.1em solid rgba(63,192,194, 0.2);
    border-bottom: 1.1em solid rgba(63,192,194, 0.2);
    border-left: 1.1em solid rgba(63,192,194, 1);
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation: load8 1.1s infinite linear;
    animation: load8 1.1s infinite linear;
  }
  @-webkit-keyframes load8 {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @keyframes load8 {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  .bullets {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    justify-content: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    width: 50%;
    margin: auto;
    margin-bottom: 30px;
    .bullet {
      padding: 5px;
      @media (max-width: $screen-xs-max) {
        padding: 6px;
      }
      font-size: 40px;
      cursor: pointer;
      color: #999;
      &.selected {
        color: $brand-turqoise;
      }
    }
  }

  .mission-examples {
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    margin: auto;
    width: 500px;
    height: 270px;

    @media (max-width: 1100px) {
      height: 430px;
      padding: 0 5%;
    }
    @media (max-width: 800px) {
      height: 320px;
    }
    @media (max-width: $screen-xs-max) {
      width: 100%;
      height: 220px;
      padding: 0;
    }
    .arrow-left, .arrow-right {
      height: 100%;
      cursor: pointer;
      align-items: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      z-index: 300;
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      img {
        width: 20px;
      }
      padding: 15px;
    }
    .reveal-item {
      position: absolute;
      left: 0;
      opacity: 0;
      pointer-events: none;
      top: 0;
      width: 100%;
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      align-items: baseline;
      -webkit-box-align: baseline;
      -ms-flex-align: baseline;
      transition: opacity .5s ease-in-out;
      -moz-transition: opacity .5s ease-in-out;
      -webkit-transition: opacity .5s ease-in-out;
      img {
        width: 100%;
      }
      .item {
        width: 100%;
      }
    }
    .reveal-item-0 {
      opacity: 1;
      pointer-events: auto;
    }
    .reveal-item-1,
    .reveal-item-2,
    .reveal-item-3 {
      top: 0;
      opacity: 0;
    }
    .carousel {
      overflow: hidden;
      width: 100%;
    }
  }
}
.ui-autocomplete {
  padding: 0;
  list-style: none;
  background-color: #fff;
  width: 218px;
  border: 1px solid #B0BECA;
  max-height: 240px;
  overflow-x: hidden;
}
.ui-autocomplete .ui-menu-item {
  border-top: 1px solid #B0BECA;
  display: block;
  padding: 4px 6px;
  color: #353D44;
  cursor: pointer;
}
.ui-autocomplete .ui-menu-item:first-child {
  border-top: none;
}
.ui-autocomplete .ui-menu-item.ui-state-focus {
  background-color: #D5E5F4;
  color: #161A1C;
}
.ui-helper-hidden-accessible {
  display: none;
}

.item {
  position: relative;
}

.hand-cursor {
  cursor: pointer;
}

.video-overlay {
  cursor: pointer;
  position: absolute;
  background-repeat: no-repeat;
  background-size: 80px;
  background-position: 50%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: url("/assets/images/getty/play_button.png");
}

.pac-container {
  min-width: 342px;
}
.pac-item {
  width: 100%;
}
.pac-item:hover {
  background-color: #eee;
}
