.dashboard-infographic-wrapper {
  @include bottom-shadow;
  z-index: 2;
  position: relative;
  &.show-ar-image {
    background-image: url("/assets/images/getty/ar_background_image_desktop.png");
    background-size: cover;
    background-position: center;
    .dashboard-infographic .left-arrow,
    .dashboard-infographic .right-arrow,
    .collapse-arrow {
      color: #ddd;
    }
    .icon-Arrow2_down {
      color: #999;
    }
    @media (max-width: $screen-xs-max) {
      background-image: url("/assets/images/getty/ar_background_image_mobile.png");
    }
  }
  &.show-getty-image {
    background-image: url("/assets/images/getty/getty_banner_1280x230.png");
    background-size: cover;
    background-position: center;
    .dashboard-infographic .left-arrow,
    .dashboard-infographic .right-arrow,
    .collapse-arrow {
      color: #ddd;
    }
    .icon-Arrow2_down {
      color: #999;
    }
    @media (max-width: $screen-xs-max) {
      background-image: url("/assets/images/getty/getty_banner_mobile_dashboard@2x.png");
    }
  }
  background-color: white;
  .dashboard-infographic.collapsed {
    height: 0px;
    background: $extra-light-gray;
  }
  .dashboard-infographic {
    position: relative;
    color: $text-gray;
    font-size: 14px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    transition: height 0.5s ease;
    height: 200px;
    @media (max-width: $screen-sm-max) {
      height: 300px;
    }
    @media (max-width: $screen-xs-max) {
      font-size: 11px;
      height: 370px;
    }
    .info-row {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin: 0;
      @media (max-width: $screen-xs-max) {
        flex-direction: column;
      }
    }
    .title {
      margin-top: 10px;
      margin-left: 10px;
      font-size: 28px;
      color: $brand-orange;
      @include headline-font;
      text-transform: uppercase;
      @media (max-width: $screen-xs-max) {
        font-size: 20px;
      }
    }
    .collapsed-title {
      cursor: pointer;
      margin: 0;
      position: absolute;
      left: 40px;
      z-index: 2;
      top: -3px;
      opacity: 0;
      @media (max-width: $screen-xs-max) {
        left: 10px;
        top: 3px;
      }
    }
    .expanded {
      transition: opacity 0.5s linear 0.6s;
      opacity: 1;
    }
    .small-title {
      font-size: 28px;
      color: $text-turqoise;
      @include headline-font;
      text-transform: uppercase;
      @media (max-width: $screen-xs-max) {
        font-size: 16px;
      }
    }
    .info-img {
      width: 140px;
      float: left;
      @media (max-width: $screen-xs-max) {
        width: 97px;
      }
    }
    .img-small {
      width: 100px;
      @media (max-width: $screen-xs-max) {
        width: 50px;
        margin: 0 10px 20px;
      }
    }
    .item {
      max-width: 400px;
      @media (max-width: $screen-xs-max) {
        padding-top: 10px;
        width: 100%;
      }
    }
    .left-arrow, .right-arrow {
      color: grey;
      font-size: 30px;
      height: 100%;
      line-height: 5;
      transition: margin-top 0.5s;
      cursor: pointer;
      margin: auto;
      padding: 0 15px;
      margin-top: 40px;
      position: relative;
      @media (max-width: $screen-xs-max) {
        padding: 0;
        margin-top: 0;
        top: 70px;
      }
    }
    .left-arrow.collapsed, .right-arrow.collapsed {
      margin-top: -130px;
    }
    .info-page-wrapper {
      position: relative;
      overflow: hidden;
      padding: 0;
    }
    .info-page {
      position: absolute;
      height: 100%;
      width: 100%;
      left: 0;
      transition: left 0.5s ease;
    }
    .hidden-right {
      left: 100%;
    }
    .hidden-left {
      left: -100%;
    }
  }
  .container.bullets {
    display: flex;
    position: relative;
    height: 30px;
    transition: margin-top 0.2s ease;
  }
  .container.bullets.collapsed {
    background-color: $extra-light-gray;
  }
  .bullets {
    margin: 0;
    padding: 0;
    width: 100%;
    position: absolute;
  }
  .row.bullets {
    padding-left: 30px;
    font-size: 20px;
    color: #ccc;
    width: 75px;
  }
  .selected {
    color: $brand-orange;
  }
  .collapse-arrow, .expand-arrow {
    margin: 2px auto 0;
    color: #aaa;
    cursor: pointer;
    font-size: 30px;
  }
  .collapse-arrow.icon-Arrow2_up {
    font-size: 40px;
    margin-top: -4px;
  }

  .page0 {
    display: flex;

    justify-content: space-between;
    align-items: center;
    padding-top: 30px;
    padding-left: 24px;
    padding-right: 24px;
    @media (max-width: $screen-xs-max) {
      padding-top: 0px;
    }
    .ar-text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      @media (max-width: $screen-xs-max) {
        width: 286px;
      }

      p {
        color: #fff;
        font-size: 20px;
        margin-top: 20px;
        margin-bottom: 0;
      }
      img {
        width: 172px;
      }
    }
    .ar-buttons {
      display: flex;
      align-items: center;
      flex-direction: column;
      .turquoise-button {
        margin-bottom: 20px;
      }
      .app-store-badge {
        width: 133px;
      }
    }

    @media (max-width: $screen-xs-max) {
      flex-direction: column;
      .ar-text {
        margin-top: 35px;
        align-items: center;
        text-align: center;
        p {
          font-size: 16px;
          margin-top: 15px;
        }
        img {
          width: 160px;
        }
      }
    }

  }

  .page1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 10px;
    align-items: center;
    font-size: 14px;
    .getty-headline {
      display: inline;
      font-size: 25px;
      text-align: center;
      @media (max-width: $screen-xs-max) {
        font-size: 20px;
        img {
          width: 100px;
        }
      }
      img {
        width: 139px;
      }

    }
    p {
      width: 500px;
      text-align: center;
      @media (max-width: $screen-xs-max) {
        padding: 15px 0;
        font-weight: 400;
        width: 250px;
      }
    }
    img {
      width: 180px;
    }
    color: white;
  }
}

.page2 {
  padding-top: 10px;

  font-size: 13px;
}
