.gps-metadata-missing-modal {
  width: 450px;
  text-align: center;

  .header {
    font-weight: 500;
    margin-bottom: 10px;
    font-size: 16px;
  }

  .body {
    margin: 10px 0;
  }

  button {
    background: #3ec0c2;
    color: white;
    font-family: roboto;
    font-size: 15px;
    font-weight: 300;
    padding-top: 0;
    align-items: center;
    height: 40px;
    width: 125px;
    margin-top: 10px;
    border: 0;
  }
}

.client-mission {
  border-top: 2px solid #eee;
  width: 100%;
  #map-canvas {
    height: 500px;
    @media (max-width: $screen-xs-max) {
      height: 200px;
    }
  }
  position: absolute;
  top: 70px;
  @media (max-width: $screen-xs-max) {
    height: 200px;
    position: relative;
    top: 0;
  }

  background: $extra-light-gray;
  height: 100%;
  width: 100%;

  .mission-content {
    background: $extra-light-gray;
    @media (min-width: $screen-sm-min) {
      padding: 10px 50px;
      // this is a hack to cover up the white blank space left by the margin of the .save-comment-button
      padding-bottom: 20px;
    }
  }
  .training-mission {
    display: flex;
    padding: 20px !important;
    margin-top: 20px;
    @media (max-width: $screen-xs-max) {
      border-bottom: 3px solid #eee;
      padding-bottom: 30px!important;
      padding-top: 30px!important;
      margin-top: 15px!important;
    }
    img {
      margin-right: 20px;
      width: 24px;
      height: 33px;
    }
    color: #1D2D47;
    .training-mission-details {
      height: 40px;
      @media (max-width: $screen-xs-max) {
        height: auto;
      }
    }
    .training-mission-label {
      font-size: 16px;
      font-weight: 500;
    }
  }

  .mission-details {
    margin-top: 52px;
    &.on-hold {
      margin-top: 0px;
    }
    @media (max-width: $screen-xs-max) {
      margin-top: 2px;
    }
    display:  flex;
    flex-direction: column;
    flex-grow: 2;
    .laanc-authorization {
      font-size: 18px;
      color: #747373;

      justify-content: flex-start!important;
      padding-left: 20px!important;
      margin-top: 16px;
    }
    .skinny-browser-laanc {
      text-align: left;
    }
    .laanc-authorization-details {
      @media (max-width: $screen-xs-max) {
        text-align: center;
        align-items: center;
      }
      font-size: 16px;
      height: auto!important;
      padding: 25px!important;
      padding-left: 20px!important;

      color: #777777;
      p {
        margin-bottom: 0px;
      }
      a {
        line-height: 30px;
        color: #3FC0C2;
        text-decoration: underline;
      }
      .turquoise-button {
        justify-content: center;
        align-items: center;
        align-self: center;
        width: 300px;
        background: white;
        text-decoration: none;
        box-shadow: none;
        margin-top: 10px;
        margin-bottom: 10px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .mission-details-box {
      background: white;
      padding: 5px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      border-bottom: 2px solid $extra-light-gray;
      height: 72px;
      .mission-details-box-label {
        display: flex;
        margin: 3px 0 5px 5px;
        @media (max-width: $screen-xs-max) {
          justify-content: flex-start;
          margin-left: 5px;
        }
        width: 175px;
        @media (min-width: $screen-lg-min) {
          font-size: 16px;
        }
        .icon-Payment {
          margin-right: 8px;
          font-size: 24px;
          margin-top: -3px;
          @media (min-width: $screen-lg-min) {
            margin-top: -2px;
          }
        }
        .icon-Calendar {
          margin-right: 8px;
          font-size: 24px;
          margin-top: -4px;
          @media (min-width: $screen-lg-min) {
            margin-top: -3px;
          }
        }
      }
      .mission-details-box-content {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        font-size: 24px;
        background-color: white;
        color: $brand-orange;
        width: 100%;
        flex-grow: 2;
        height: 100%;
        border-left: 2px solid $extra-light-gray;
        .mission-details-box-content-above {
          width: 100%;
          text-align: center;
        }
        .mission-details-reschedule-link {
          cursor: pointer;
          color: #45abad;
          text-decoration: underline;
          font-size: 12px;
        }
        .mission-details-box-content-below {
          width: 100%;
          text-align: center;
          font-size: 12px;
          color: $text-gray;
        }
      }
      .mission-details-box-bottom-row {
        @include headline-font;
        font-size: 18px;
      }
      .status-button {
        .laanc-exception {
          font-size: 24px;
          color: black;
        }
        .status-icon-cell.icon-Checkmark_b, .status-icon-cell.icon-Checkmark_c {
          @media (min-width: $screen-lg-min) {
            font-size: 24px;
          }
        }
        &:last-child {
          padding-left: 10px;
          @media (min-width: $screen-lg-min) {
            padding-left: 40px;
          }
        }
        .status-icon-text {
          @media (min-width: $screen-lg-min) {
            font-size: 16px;
          }
          @media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
            font-size: 10px;
          }
        }
      }
    }
    table.mission-details-table {
      width: 100%;
      background-color: white;
      tr {
        border-bottom: 2px $extra-light-gray solid;
        .mission-details-label {
          border-right: 2px $extra-light-gray solid;
          padding-left: 0;
          font-size: 13px;
        }
        .mission-details-label-icon {
          font-size: 18px;
        }
        .mission-details-reschedule-link {
          cursor: pointer;
          text-decoration: underline;
        }
        .mission-details-price {
          color: $brand-orange;
        }
        td {
          height: 60px;
          vertical-align: middle;
          padding: 10px;
        }
      }
    }
  }
  @media (min-width: $screen-xs-max) {
    .instructions-content {
      display: flex;
      flex-direction: row;
      align-items: stretch;
    }
    .instructions {
      display: flex;
      flex-direction: column;
    }
    .additional-col {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    .additional-info {
      flex-grow: 2;
    }
  }
  @media (max-width: $screen-xs-max) {
    .instructions {
      padding: 0;
      .instructions-panel.contact-panel {
        flex-direction: column;
      }
    }
    h3 {
      margin: 18px;
    }
  }
  h3 {
    color: $brand-darkblue;
    margin-top: 15px;
    @media (max-width: $screen-xs-max) {
      font-size: 18px;
    }
  }
  .count {
    font-size: 14px;
    color: #777;
  }
  #flight-app-information {
    padding: 15px;
    padding-left: 25px;
    padding-top: 25px;
  }

  .instructions-panel {
    padding: 5px;
    background-color: white;
    color: $text-darkblue;
    overflow-x: auto;
    margin-right: 20px;
    @media (max-width: $screen-xs-max) {
      min-height: inherit;
      margin-right: 0;
      h3 {
        padding: 18px;
      }
      .contact-panel {
        flex-direction: column;
      }
    }
    .camera-settings {
      background-color: $brand-mint-transparent;
      padding: 12px 20px;
    }
    &.contact-panel {
      padding: 20px;
      display: flex;
      flex-direction: row;
      .title {
        margin-bottom: 10px;
      }
      .divider {
        border-left: 2px solid $extra-light-gray;
        margin-left: 30px;
        padding-left: 30px;
      }
      .contact-name-panel {
        white-space: nowrap;
      }
    }
    &.additional-instructions {
      padding: 12px 20px;
    }
    .shots-panel {
      padding: 12px 20px;
      max-height: 500px;
      overflow-y: scroll;
    }
    .shots-panel-intructions {
      margin-left: 10px;
    }
    .title {
      color: $brand-orange;
      font-weight: bold;
    }
  }
  .additional-info {
    background-color: white;
    padding: 12px 20px;
    height: 125px;
    overflow-x: auto;
    @media (max-width: $screen-xs-max) {
      overflow-x: none;
      height: 100%;
    }
    &.desktop {
      color: $brand-orange;
      background-color: inherit;
      height: 100px;
    }
    .desktop-icon-text {
      display: table-cell;
    }
    .desktop-icon {
      display: table-cell;
      padding-right: 10px;
      font-size: 20px
    }
  }
  .upload-steps-panel {
    background: white;
    padding: 20px;
    -webkit-box-shadow: 0 0 15px #aaa;
    -moz-box-shadow: 0 0 15px #aaa;
    box-shadow: 0 0 15px #aaa;
    display: flex;
    flex-direction: row;
    .title {
      font-size: 20px;
      color: $brand-darkblue;
    }
    .title.active {
      color: $brand-orange;
    }
    .part {
      position: relative;
      &.pointer {
        cursor: pointer;
      }
      padding: 0;
    }
    .arrow-down {
      position: absolute;
      bottom: -28px;
      left: 50%;
      width: 0;
      height: 0;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-top: 8px solid white;
    }
    .text {
      color: #888;
    }
    .separator {
      height: 80px;
      margin: auto 30px;
      width: 1px;
      border-right: 1px solid #aaa;
    }
  }
  .button-wrapper {
    display: flex;
  }
  .button {
    @include button;
    border-color: $brand-orange;
    background-color: $brand-orange;
    color: white;
    text-decoration: none;
    display: flex;
    justify-content: center;
  }
  .orange-button {
    color: white;
    background-color: $brand-orange;
    border: none;
    padding: 0 20px;
  }
  .mission-complete {
    margin-top: 60px;
    width: 100%;
    height: 420px;
    padding: 70px;
    position: relative;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    .congrats {
      color: $brand-orange;
      @include headline-font;
      font-size: 30px;
    }
    .forward {
      z-index: 1;
    }
    .chair-img {
      width: 150px;
      margin-top: 15px;
      margin-bottom: 10px;
    }
    .background1, .background2 {
      width: calc(100% - 20px);
      position: absolute;
      margin-top: 10px;
    }
    .background1 {
      background-color: $brand-mint-transparent;
      height: 250px;
      top: 0;
    }
    .background2 {
      background-color: $sand-transparent;
      height: 150px;
      top: 250px;
    }
    .completed {
      color: #333;
      text-decoration: underline;
    }
  }
  .save-comment-button {
    margin: 25px 0 200px 0;
    padding: 0 20px;
    font-family: roboto;
    font-size: 15px;
    padding-top: 0;
    align-items: center;
    height: 40px;
    width: 125px;
    box-shadow: 0 2px 1px rgba(0,0,0,0.2);
    border: none!important;
    color: white;
  }
  .final-instructions {
    width: 50%;
    padding: 10px 0 25px 0;
  }
  .pilot-comment {
    width: 50%;
    padding-top: 20px;
    textarea {
      font-size: 14px;
      height: 160px;
    }
  }
  .back-button {
    margin: 25px 0 200px 0;
    padding: 0 20px;
    font-family: roboto;
    font-size: 15px;
    padding-top: 0;
    align-items: center;
    height: 40px;
    width: 125px;
    box-shadow: 0 2px 1px rgba(0,0,0,0.2);
    border: none!important;
    background-color: #999999;
    color: white;

    margin-right: 20px;
    padding-top: 1px;
    .icon-DB-icon_Arrow-left-3 {
      line-height: 1.3;
    }
  }
  .form-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px;
  }

  .on-hold-panel {
    display: flex;
    flex-direction: row;

    .on-hold-icon {
      font-size: 30px;
      margin: 10px;
    }
    .on-hold-content {
      flex-direction: column;
      margin: 10px;
    }
    i, .title {
      color: #FDA500;
    }
  }
}

.map-wrapper {
  position: relative;
  .kml-button {
    position: absolute;
    top: 9px;
    right: 45px;
    .white-button {
      height: 40px;
      padding-left: 20px;
      padding-right: 20px;
      @media (max-width: $screen-xs-max) {
        height: 30px;
      }
    }
  }
  .client-notes {
    @media (max-width: $screen-xs-max) {
      position: relative;
      left: 0;
      @include bottom-shadow;
    }
    font-size: 13px;
    position: absolute;
    top: 0;
    left: 10px;
    background: white;
    z-index: 2;
    border-top: 4px solid $brand-turqoise;
    .down-arrow {
      border-top: 8px solid $brand-turqoise;
      position: absolute;
      width: 0;
      height: 0;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      right: 32px;
      top: 0;
    }
  }
  .client-note, .client-title {
    background: white;
    width: 300px;
    padding: 8px 15px;
    @media (max-width: $screen-xs-max) {
      width: 100%;
    }
  }
  .text {
    white-space: pre-line;
  }
  .client-note-marker {
    color: white;
    font-size: 19px;
    position: relative;
    &.polygon {
      top: 13px;
    }
    &.point {
      left: -14px;
      top: -1px;
      &::before {
        position: relative;
        left: 20px;
        top: 8px;
        content: $icon-Bubble_b;
        font-size: 28px;
        font-family: $dronebase-icon-font;
        color: $brand-yellow;
        z-index: -1;
      }
    }
  }
  .arrow {
    font-size: 24px;
    position: relative;
    top: 6px;
    left: -2px;
  }
  .divider {
    height: 1px;
    border-top: 1px solid $brand-orange;
    margin: 0 5px;
  }
  .client-note {
    display: flex;
    color: #777;
    cursor: pointer;
    .bubble, .note-id {
      color: #888;
    }
    .bubble {
      font-size: 20px;
      position: relative;
      top: 4px;
      left: 3px;
    }
    .icon-Bubble_b {
      display: none;
      color: $brand-yellow;
    }
    .note-id {
      position: relative;
      left: -13px;
      top: -3px;
      font-size: 10px;
    }
    .text {
      margin-top: 3px;
      margin-left: 5px;
      width: 230px;
    }
    &.highlight {
      background: #fce0da;
      .icon-Bubble_a {
        display: none;
      }
      .icon-Bubble_b {
        display: inline;
      }
      .note-id {
        color: white;
      }
    }
  }
  .client-title {
    font-weight: normal;
    cursor: pointer;
    position: relative;
    top: -3px;
    left: 3px;
    background: transparent;
  }
  .client-title .icon-Bubble_b {
    color: $brand-yellow;
    position: relative;
    font-size: 20px;
    top: 4px;
    left: 10px;
  }
  .client-notes-wrapper {
    &.shrink {
      height: 0;
    }
    @media (max-width: $screen-xs-max) {
      max-height: 500px;
    }
    max-height: 800px;
    overflow-y: scroll;
    overflow-x: hidden;
  }
}
