.uploader-top-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
  .btn {
    margin: 0 25px;
  }
  .progressMeter {
    width: 100px;
    height: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    .completed {
      width: calc(40%);
      height: 10px;
      background-color: $brand-orange;
    }
    .not-completed {
      width: calc(60%);
      background-color: #aaa;
      height: 10px;
    }
  }
}
.uploader-top-row-left {
  display: flex;
  justify-content: flex-start;
}
.uploader {
  background: white;
  color: #999;
  .upload-target {
    min-height: 300px;
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 5px;
  }
  .outline {
    display: flex;
    justify-content: center;
    outline: 2.5px dashed #ccc;
    outline-offset: -15px;
  }
  .img-label {
    position: relative;
  }
  .uploading {
    position: relative;
  }
  .asset-viewer {
    min-height: 100px;
  }
  .upload-instructions {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0;
  }
  .files {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 100%;
  }
  .upload-button {
    background-color: $brand-orange;
    border: none;
    color: $extra-light-gray;
    @include headline-font;
    font-size: 24px;
    margin: 15px;
    padding: 5px 20px;
  }
  .large-text {
    font-size: 30px;
  }
  .upload-icon {
    width: 140px;
    height: 140px;
    margin-bottom: -40px;
  }
  .asset-uploader {
    width: 100%;
  }
  .is-dragover {
    background-color: grey;
  }

}
