.client-mission-uploader {
  display: flex;
  flex-wrap: nowrap;
  margin-top: 10px;
  padding: 0;
  .shot-list {
    flex-basis: 20%;
    display: flex;
    flex-direction: column;
    margin-right: 4px;
    padding-top: 15px;
    border-top: 2px solid #bbb;
    max-height: 90vh;
    overflow: auto;
    .folder-icon {
      width: 20px;
    }
    .shot {
      padding: 8px 10px;
      position: relative;
      border: 1px solid transparent;
      z-index: 0;
      display: flex;
    }
    .checkmark {
      position: absolute;
      color: $brand-turqoise;
      left: 0;
      top: 10px;
    }
    .notready-checkmark {
      margin-right: 5px;
      color: $brand-orange;
      left: 0;
      top: 10px;
    }
    &.hide-notready .notready-checkmark {
      display: none;
    }
    .shot.selected {
      color: $text-turqoise;
    }
    .shot:hover {
      cursor: pointer;
    }
    .shot:drag-over {
      border: 1px solid red;
    }
    .shot-name {
      margin-left: 10px;
    }
  }
  .background  {
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .shot.selected .background {
    background-color: #ddd;
  }
  .draggable-object-target.accepts-drag .background {
    background-color: grey;
  }
  .assets {
    flex-basis: 80%;
    padding-left: 15px;
    .asset-list {
      background-color: white;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      min-height: 280px;
      padding: 4px;
    }
    .draggable-object {
      border: 1px solid transparent;
      padding: 0;
    }
    .button {
      margin: 0;
      height: 30px;
    }
  }
  .uploaded-content {
    width: 100%;
    max-height: 620px;
    overflow-y: scroll;
  }
  .thumbnail-view {
    display: flex;
    flex-wrap: wrap;
  }


  .list-view-assets {
    .shot-asset {
      width: calc(100% - 40px)!important;
      margin-right: 20px;
      margin-left: 13px;
      border-bottom: 1px solid #DDDDDD;
    }
    .shot-asset:last-of-type {
      border-bottom: none;
      margin-bottom: 40px;
    }
    .delete-asset {
      padding-bottom: 10px;
    }
  }

  .uploader-header {
    width: 100%;
    justify-content: space-between;
    display: flex;
    margin-left: 30px;
    margin-top: 30px;
    margin-right: 25px;
    margin-bottom: 10px;
    .current-shot-type {
      font-family: roboto;
      font-size: 20px;
      font-weight: normal;
      margin-bottom: 10px;
    }
    .missing-gps-info {
      a {
        color: grey;
        text-decoration: underline;
        font-style: italic;

        i {
          color: #fdb331;
        }
      }
    }
    .currently-uploading {
      font-weight: light;
      font-size: 13px;
      font-color: #999;
    }
    .list-view-switcher {
      cursor: pointer;
      width: 82px;
      img {
        width: 82px;
      }
    }
  }

  .list-view-header {
    display: flex;
    width: 100%;
    margin-left: 17px;
    border-bottom: 1px solid #DDDDDD;
    margin-right: 25px;

    height: 100%;
    padding-bottom: 10px;
    .header-filename {
      padding-left: 13px;
      width: 50%;

    }
  }

  #shot-comment {
    margin-top: 20px;
    padding-top: 40px;
    display: none;
    min-height: 200px;
    .instruction {
      margin-bottom: 10px;
      color: $text-darkblue;
      font-size: 16px;
    }
    .form-control {
      width: 50%;
      color: #222 !important;
    }
    .save-comment-button {
      margin: 25px 0 50px 0;
      padding: 0 20px;
      font-family: roboto;
      font-size: 15px;
      padding-top: 0;
      align-items: center;
      height: 40px;
      width: 200px;
      box-shadow: 0 2px 1px rgba(0,0,0,0.2);
      border: none!important;
      background-color: #999999;
      color: white;
    }
    textarea {
      font-size: 14px;
      height: 160px;
    }
  }
  .delete-shot {
    position: absolute;
    right: 3px;
  }
  .asset-count {
    color: #777;
    margin-left: 8px;
  }
  .upload-instructions {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0;
    color: #bbb;
    border: 3px dashed #ddd;
    margin: 12px;
    min-height: 250px;
  }
  .upload-icon {
    width: 240px;
    margin-bottom: -60px;
    margin-top: -60px;
  }
  .large-text {
    font-size: 30px;
  }
  .is-dragover {
    background-color: grey !important;
  }
  .nice-select {
    margin-top: 5px;
    border-radius: 0;
    border: 1px solid #ccc;
  }
  .nice-select:after {
    height: 10px;
    width: 10px;
    margin-top: -6px;
  }
  .nice-select .list {
    display: none;
  }
  .nice-select.open .list {
    border-radius: 0;
    margin-top: 1px;
    display: block;
  }
  .note {
    color: #999;
    margin-top: 20px;
  }
  .vjs-big-play-button {
    display: none;
  }
  .vjs-poster::after {
    content: "\2a"; // play button
    font-family: $dronebase-icon-font !important;
    font-size: 3vw;
    position: absolute;
    top: 30%;
    left: 38%;
  }
}

.client-uploader-top-row {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-end;
  margin: 20px 0 20px 0;
  .button {
    margin: 0 0 0 10px !important;
  }
  .left-container {
    display: flex;
    align-items: flex-end;
    position: relative;
    flex-basis: 60%;
    margin-left: 20px;
    .button {
      font-family: roboto;
      font-size: 15px;
      padding-top: 0;
      align-items: center;
      height: 40px;
      width: 125px;
      box-shadow: 0 2px 1px rgba(0,0,0,0.2);
    }

  }
  .right-container {
    display: flex;
    align-items: flex-end;
    flex-basis: 50%;
    justify-content: flex-end;
    .button {
      font-family: roboto;
      font-size: 15px;
      padding-top: 0;
      align-items: center;
      height: 40px;
      width: 125px;
      box-shadow: 1px 2px 1px rgba(0,0,0,0.2);
    }
    .bulk-upload {
      cursor: pointer;
      color: $dark-gray;
      margin-bottom: 5px;
    }
  }
  .progress-container {
    width: 90%;
    position: relative;
    height: 40px;
    align-self: center;
    background-color: white;
    padding-top: 5px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.3);
    .uploading-stats {
      font-family: roboto;
      margin-left: 20px;
      padding-top: 5px;
      padding-bottom: 6px;
      font-size: 15px;
      span {
        font-size: 12px;
        color: #999999;
      }
    }
  }
  .progress-bg {
    width: 100%;
    background-color: #ccc;
    height: inherit;
    height: 3px;
    z-index: 1;
    @media (max-width: $screen-xs-max) {
      width: 99%;
    }
  }
  .progress-bg-bar {
    width: 50%;
    background-color: $brand-turqoise;
    height: 3px;
    left: 0;
    z-index: 2;
  }
  .progress-space {
    position: relative;
    width: 20%;
    border-right: 2px solid $extra-light-gray;
    height: 8px;
    float: left;
    z-index: 3;
    top: -3px;
  }
  .progress-count {
    @include headline-font;
    margin-bottom: 0 !important;
    font-size: 22px;
    color: #999;
    margin-left: 10px;
  }
  .pct {
    font-size: 16px;
  }
  .shot-instructions {
    padding-top: 32px;
    font-size: 18px;
    flex-basis: 20%;
    color: $text-darkblue;
  }
  .small-text {
    font-size: 14px;
    color: #999;
  }
  .next-button .icon-DB-icon_Arrow-right-3 {
    padding-left: 8px;
    padding-top: 2px;
  }
  .delete-assets-button {
    margin: 0 20px 0 0 !important;
    width: 140px !important;
  }
  .form-control {
  }
  .form-control::-webkit-input-placeholder,
  .form-control:-moz-placeholder,
  .form-control::-moz-placeholder,
  .form-control:-ms-input-placeholder   {
    color: #999;
    opacity: 1;
  }
}
.icon-db_upload_icon {
  padding-left: 6px;
}
.pilot-comment .form-control {
  color: #222 !important;
}
