.upload-file {
  display: flex;
  flex-direction: column;
}
.shot-asset {
  opacity: 1 !important;
  overflow: hidden;
  width: 20%;
  .checkmark {
    color: #3fc0c2;
  }
}
.processing-wrapper {
  width: 100%;
  height: 8vw;
  background-color: #efefef;
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.loading {
  width: 4vw;
  height: 4vw;
}
.uploading, .filename {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
  .name {
    width: 90%;
    height: 4.5em;
    font-size: 12px;
  }
}
.filename, .table-filename {
  margin-top: 8px;
  line-height: 1.5;
  padding: 0 10px;
  position: relative;
  word-wrap: break-word;
}
.table-filename {
  width: 100%;

  a {
    float: right;
  }
}
.delete-asset {
  margin: 0;
  padding: 0;
  right: 0;
  top: 3px;
  font-size: 12px;
  color: #999;
  position: absolute;
}
.asset {
  width: 100%;
  height: 8vw;
  background-size: cover;
  background-position: center center;
}
.asset-wrapper.selected {
  border: 3px solid $brand-orange;
}
.asset-wrapper {
  width: 100%;
  border: 3px solid transparent;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  overflow: hidden;
  .progressMeter {
    width: 0px;
    height: 2px;
    background-color: $brand-orange;
  }
  .missing-gps-info {
    i {
      color: #fdb331;
    }
  }
}

.file-information {
  display: flex;
  width: 100%;
  align-items: center;
  height: 40px;
  .list-view-row {
    width: calc(50% - 47px);
  }
  .list-view-filename {
    width: calc(50% - 20px);
    line-height: 20px;
  }
  .missing-gps-info {
    width: calc(50% - 20px);
  }
  .progress-percent {
    width: calc(50% - 30px);
  }
  .delete-asset {
    position: relative;
    right: 0px;
  }
  .status-icon {
    font-size: 20px;
    margin-left: 17px;
    margin-right: 20px;
  }
}

.uploaded-text {
  color: lightgrey;
}

.loader-status,
.loader-status:after {
  border-radius: 50%;
  width: 1em;
  height: 1em;
}
.loader-status {
  margin: 60px auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 0.2em solid #eeeeee;
  border-right: 0.2em solid #eeeeee;
  border-bottom: 0.2em solid #eeeeee;
  border-left: .2em solid #3fc0c2;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.bulk-upload-link {
  cursor: pointer;
  padding-bottom: 9px;
  color: #149B9E;
  font-weight: 400;
  &:hover {
    color: #149B9E;
  }
}
