.login-form {
  background: url("/assets/images/v2/Login_Background.jpeg") no-repeat center center fixed;
  background-size: cover;
  top: 0;
  bottom: 0;
  position: absolute;
  width: 100%;
  .container {
    height: 100%;
    .row {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin: 0 15px;
      .container-content {
        background-color: rgba(0, 0, 0, 0.3);
        padding: 0 15px;
        .page-header {
          display: flex;
          justify-content: center;
          border: none;
          flex-direction: column;
          align-items: center;
          .divider {
            margin: 10px;
            width: 50px;
            border-top: 1px solid $brand-orange;
          }
        }
        .close-button {
          color: white;
          position: absolute;
          right: 0;
          margin: 10px;
        }
        .close-button:hover {
          text-decoration: none;
        }
        .form-password {
          position: relative;
          .forgot-password {
            color: #aaa;
            position: absolute;
            right: 15px;
            top: 15px;
          }
        }
        .error {
          color: $brand-orange;
        }
        .input-lg {
          background-color: transparent;
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: 1px solid $brand-orange;
          padding-left: 5px;
        }
        .login-button, .signup-button {
          @include button;
          background-color: transparent;
          margin-bottom: 30px;
        }
        .login-button {
          color: $brand-orange;
          border: 1px solid $brand-orange;
        }
        .signup-button {
          color: $brand-turqoise;
          border: 1px solid $brand-turqoise;
        }
        .login-button:hover {
          background-color: $brand-orange;
          color: black;
          text-decoration: none;
        }
        .signup-button:hover {
          background-color: $brand-turqoise;
          color: black;
          text-decoration: none;
        }
        .buttons {
          display: flex;
          flex-direction: row;
          justify-content: center;
          padding-left: 15px;
        }
      }
    }
  }
}

.input-lg {
  color: #ccc;
}
