.clients-page {
  font-family: roboto;
  color: #1F2B45;
  .header-image {
    background-image: url("/assets/images/clients/client_mission_header_image.jpg");
    background-position: center;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 750px;
    padding: 150px;
    @media (max-width: $screen-xs-max) {
      background-image: url("/assets/images/clients/client_mission_header_image_mobile.jpg");
      height: 250px;
      padding: 60px;
    }
    h2 {
      font-size: 50px;
      @media (max-width: $screen-xs-max) {
        font-size: 30px;
      }
    }
    p {
      font-size: 25px;
    }
    .steps-row {
      display: flex;
      .steps-group {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 30px;
        img {
          height: 50px;
        }
        .step-number {
          font-weight: 400;
          margin-top: 10px;
          font-size: 18px;
        }
        .step-text {
          font-size: 14px;
        }
      }
    }
  }
  .how-to-become {
    padding: 25px;
    padding-bottom: 0;
    .how-to-become-header {
      text-align: center;
      font-size: 18px;
      font-weight: 500!important;
      margin-bottom: 20px;
      margin-top: 0;
    }
    .steps-group-row:first-of-type {
      box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.16);
    }
    .steps-group-row {
      height: 50px;
      padding-left: 20px;
      padding-right: 15px;
      box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
      border-radius: 4px;
      display: flex;
      margin-bottom: 5px;
      align-items: center;

      .step-number {
        font-size: 18px;
        font-weight: 500;
        margin-right: 15px;
      }
      .step-text {
        width: calc(100% - 50px);
        font-size: 14px;
        font-weight: 400;
      }
      img {
        height: 40px;
      }
    }
  }
  .limit-width {
    max-width: 1440px;
    margin: auto;
    padding-left: 50px;
    padding-right: 50px;
    @media (max-width: $screen-sm-max) {
      max-width: none;
      padding-left: 0;
      padding-right: 0;
      margin-left: 50px;
      margin-right: 50px;
    }
    @media (max-width: $screen-xs-max) {
      padding-left: 0;
      padding-right: 0;
      margin-left: 20px;
      margin-right: 20px;
    }
    .client-page-section {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 50px;
      margin-left: auto;
      margin-right: auto;
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 60px;
      text-align: center;
      border-bottom: 2px solid #ddd;
      font-size: 18px;
      line-height: 26px;
      @media (max-width: $screen-xs-max) {
        width: 100%;
        margin-top: 40px;
        font-size: 16px;
        padding-left: 10px;
        padding-right: 10px;

      }

      .section-header {
        font-size: 35px;
        font-weight: 400!important;
        margin-bottom: 25px;
        @media (max-width: $screen-xs-max) {
          font-size: 22px;
        }
      }
      img {
        width: 200px;
        margin-bottom: 10px;
      }
      .download-buttons {
        .app-store-badge {
          width: 175px;
          margin-right: 15px;
          margin-bottom: 0;
          @media (max-width: $screen-xs-max) {
            width: 145px;
            margin-right: 7px;
          }
        }
      }

    }
    .complete-profile {
      .centered-text {
        max-width: 666px;
        margin-bottom: 50px;
        @media (max-width: $screen-xs-max) {
          margin-bottom: 30px;
        }
      }
      .wide-button {
        width: 230px;
      }
      .subtext {
        max-width: 550px;
        margin-bottom: 60px;
        color: #777777;

        @media (max-width: $screen-xs-max) {
          margin-bottom: 35px;
        }
      }
      .emphasize {
        font-size: 25px;
        color: #E36E4D;
        font-weight: 500;
      }
    }
    .license {
      .centered-text {
        max-width: 698px;
        margin-bottom: 60px;
        font-size: 16px;
        font-weight: lighter;
      }
      .card-block {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .card {
          width: 450px;
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-bottom: 30px;

          @media (max-width: $screen-xs-max) {
            width: 100%;
          }
          img {
            margin-top: 25px;
            margin-bottom: 30px;
          }
          .subtext {
            text-align: left;
            margin-bottom: 20px;
            color: #777777;
          }
          .turquoise-button {
            width: 100%;
          }
          &.has-license {
            .subtext {
              margin-top: 30px;
            }
          }
        }
      }
    }
    .download {
      .centered-text {
        max-width: 646px;
        margin-bottom: 40px;
        @media (max-width: $screen-xs-max) {
          margin-bottom: 30px;
        }
      }
      .benefits-of-download {
        display: flex;
        margin-top: 95px;
        flex-wrap: wrap;
        width: 100%;
        justify-content: center;
        @media (max-width: $screen-sm-max) {
          justify-content: center;
        }
        @media (max-width: $screen-xs-max) {
          display: block;
          margin-top: 0px;
          width: 100%;
        }
        .benefit {
          width: 320px;
          margin-right: auto;
          margin-left: auto;

          @media (max-width: $screen-xs-max) {
            margin-right: 0;
            margin-left: 0;
            margin: 0;
            display: flex;
            flex-direction: column;
            width: 100%;
            align-items: center;
          }
          .subtext {
            font-size: 16px;
            text-align: left;
          }
          .benefit-headline {
            font-size: 24px;
            margin-top: 20px;
            margin-bottom: 12px;
            font-weight: 400;
            width: 100%;
            text-align: left;
            @media (max-width: $screen-xs-max) {
              margin-top: 50px;
              font-size: 20px;
            }
          }
          img {
            width: 250px;
            @media (max-width: $screen-xs-max) {
              height: auto;
              width: 130px;
              margin-top: 35px;
            }
          }
          .last {
            width: 90%;
          }
        }
      }
    }
    .get-experience {
      .section-header {
        @media (max-width: $screen-xs-max) {
          width: 237px;
        }
      }
      .subtext {
        max-width: 640px;
      }
      img {
        width: 614px;
        margin-top: 50px;
        margin-bottom: 50px;
        @media (max-width: $screen-xs-max) {
          width: 340px;
          margin-top: 30px;
          margin-bottom: 30px;
        }
      }
      .turquoise-button {
        width: 231px;
      }
    }
    .client-page-apps {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-top: 40px;
      font-size: 16px;
      .download-buttons {
        margin-top: 20px;
        margin-bottom: 80px;

        @media (max-width: $screen-xs-max) {
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          margin-top: 25px;
          margin-bottom: 40px;
        }

        .app-store-badge {
          width: 175px;
          margin-right: 15px;

          @media (max-width: $screen-xs-max) {
            width: 193px;
            margin: 0;
            margin-bottom: 20px;
          }
        }
      }
    }
    .base-cards {
      margin-left: auto;
      max-width: 1150px;
      margin-right: auto;
      padding-bottom: 200px;
      @media (max-width: $screen-xs-max) {
        padding: 20px;
        padding-bottom: 100px;
      }
      .card {
        margin-bottom: 20px;
        @media (max-width: $screen-xs-max) {
          flex-direction: column;
          justify-content: center;
          align-items: center;
          text-align: center;
        }
        display: flex;
        padding: 0;
        overflow: hidden;
        object-fit: cover;
        text-align: left;
        img {
          max-width: 60%;
          object-fit: cover;
          @media (max-width: $screen-xs-max) {
            width: 100%;
            max-width: 100%;
          }
        }
        .card-text {
          margin: 40px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-left: 60px;
          margin-right: 120px;
          @media (max-width: $screen-sm-max) {
            margin-left: 30px
          }
          @media (max-width: $screen-xs-max) {
            margin-left: 40px;
            margin-right: 40px;
          }

          .darkblue-button {
            margin-bottom: 0;
          }
        }
      }
    }
  }
  .centered-text {
    font-size: 18px;
    @media (max-width: $screen-xs-max) {
      font-size: 16px;
    }
  }
  .card, .newsletter {
    box-shadow: 0 5px 20px rgba(0,0,0,.35);
    border-radius: 10px;
    padding: 40px;
    margin-left: 30px;
    margin-right: 30px;

    @media (max-width: $screen-xs-max) {
      margin: 0;
      padding: 25px;
    }
    h1 {
      margin-top: 0;
      font-size: 24px;
      font-weight: 400!important;
      @media (max-width: $screen-xs-max) {
        font-size: 22px;
      }
    }
    .subtext {
      font-size: 18px;
      @media (max-width: $screen-xs-max) {
        font-size: 16px;
      }
    }
  }

}
