.request-password-reset-form {
  background: url("/assets/images/v2/ResetPassword_Background.jpeg") no-repeat center center fixed;
  background-size: cover;
  top: 0;
  bottom: 0;
  position: absolute;
  width: 100%;
  .container {
    height: 100%;
    .row {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      color: #aaa;
      .container-content {
        background-color: rgba(0, 0, 0, 0.3);
        .page-header {
          display: flex;
          justify-content: center;
          border: none;
          flex-direction: column;
          align-items: center;
          .divider {
            margin: 10px;
            width: 50px;
            border-top: 1px solid $brand-orange;
          }
        }
        .close-button {
          color: white;
          position: absolute;
          right: 0;
          margin: 10px;
        }
        .close-button:hover {
          text-decoration: none;
        }
        .error {
          color: #aaa;
        }
        .input-lg {
          background-color: transparent;
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: 1px solid $brand-orange;
          padding-left: 0;
        }
        .form-group {
          display: flex;
          justify-content: center;
          .submit-button {
            @include headline-font;
            font-size: 20px;
            background-color: transparent;
            flex-basis: 1;
            width: 25%;
            margin-right: 20px;
            text-align: center;
            line-height: 100%;
            padding: 3px 0;
            margin: 30px;
            color: $brand-turqoise;
            border: 1px solid $brand-turqoise;
          }
          .submit-button:hover {
            background-color: $brand-turqoise;
            color: black;
            text-decoration: none;
          }
        }
      }
    }
  }
}
