.pilotlog {
  border-top: 2px solid #eee;
  background-color: $extra-light-gray;
  overflow-y: scroll;
  height: 100%;
  position: absolute;
  top: 70px;
  bottom: 0;
  left: 0;
  right: 0;
  @media (max-width: $screen-xs-max) {
    top: 50px;
  }
  .map-container {
    height: 350px;
    padding: 0px;
  }
  .row {
    margin: 0;
  }
  .pilotlog-list {
    margin: 30px 60px;
    padding: 0;
    width: auto;
    @media (max-width: $screen-xs-max) {
      margin: 10px 15px;
    }
  }
  .header {
    margin: 0;
    padding-bottom: 10px;
  }
  .header, .mission-listing {
    border-bottom: 1px solid #aaa;
  }
  .mission-id {
    color: $brand-turqoise;
    .mission-icon {
      color: $text-gray !important;
      margin-left: 10px;
      @media (max-width: $screen-xs-max) {
        margin-left: 2px;
      }
      &.client-mission-icon-color {
        margin-left: 6px;
        @media (max-width: $screen-xs-max) {
          margin-left: 0;
        }
      }
      &.creative-mission-icon-color {
        font-size: 30px;
        margin-left: 6px;
        margin-top: -6px;
        vertical-align: middle;
        position: relative;
      }
    }
    .arrow {
      color: $text-gray !important;
    }
  }
  .mission-listing {
    color: #999;
    padding: 10px 0;
    transition: height, 0.5s, ease;
    @media (max-width: $screen-xs-max) {
      font-size: 12px;
    }
  }
  .top-row {
    cursor: pointer;
    margin: 12px 0;
  }
  .mission-listing.expanded {
    height: 100px;
    transition: height, 0.5s, ease;
  }
  .icon-Drone_a {
    font-size: 60px;
  }
  .arrow {
    position: absolute;
    margin-left: -20px;
    top: -2px;
    font-size: 20px;
  }
  .payment-due {
    color: $brand-orange;
  }
  .expanded-mission {
    display: none;
    background-color: white;
    margin: 0;
    padding: 10px;
    .main-image {
      height: 200px;
      @media (max-width: $screen-xs-max) {
        height: 50px;
      }
    }
    .details {
      margin: 5px 0 5px 30px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      .title {
        font-size: 18px;
        font-weight: lighter;
        color: $text-darkblue;
        margin-bottom: 10px;
      }
      .guideline-link {
        color: $brand-turqoise;
        font-style: italic;
        text-decoration: underline;
      }
      .button {
        @include button;
        color: $text-darkblue;
        border: solid 1px $text-darkblue;
        margin-top: 10px;
        &:hover {
          background-color: $text-darkblue;
          color: white;
          text-decoration: none;
        }
      }
    }
    /*
      At the time of writing this, we can't add icons through the font
      so we have to add them with SVGs. Once we can add through the font,
      transfer all SVGs to the font.
    */
    .rejected-icon-svg {
      float: left;
      display: table-cell;
      margin: 0 8px 5px 0;
      width: 20px;
      height: 20px;
    }
    .icon {
      font-size: 20px;
      float: left;
      margin: 0 8px 5px 0;
      display: table-cell;
    }
    .icon-Drone_b {
      font-size: 30px;
      margin: -5px 3px 0 -6px;
    }
    .text {
      display: table-cell;
    }
  }
}
.page-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 45px;
  border-radius: 0;
  .arrow-enabled, .arrow-disabled {
    padding: 10px;
    color: grey;
    text-decoration: none;
    font-size: 20px;
    margin-top: -1px;
  }
  .arrow-disabled {
    cursor: not-allowed;
    color: #aaa;
  }
}
