.mission-list-data {
  .training-mission-information {
    background: #eee;
    border-top: 1px solid white !important;
  }
  .pin {
    height: 35px;
  }
  .mission-list-cards:first-of-type {
    border-top: none;
  }
  .mission-list-cards {
    .mission-flight-date {
      color: #777;
      margin-top: 10px;
      &.disabled {
        background-color: white;
        color: $text-gray;
        opacity: 0.5;
      }
    }

    padding: 24px 20px;
    border-top: 1px solid #eee;
    .mission-icon {
      padding-right: 12px;
    }
    .mission-details {
      display: flex;
      .mission-location-schedule {
        flex-grow: 1;
      }
      .mission-address {
        margin-bottom: 5px;
        width: 200px;
      }
      .mission-price-status {
        text-align: right;
        .mission-price {
          font-size: 20px;
          color: #E36E4D;
        }
        .mission-status {
          color: #777;
          margin-top: 3px;
          font-size: 12px;
        }
      }
    }
    .mission-actions {
      margin: 20px 35px 0px;
    }
  }
  .mission-list-columns {
    height: 80px;
    align-items: center;
    border-top: 2px solid #eee;
  }
  .mission-location {
    display: flex;
    .pin {
      margin-left: 20px;
    }
    .mission-address {
      width: 200px;
      margin-left: 20px;
      overflow-wrap: normal;
    }
  }
  .button-bundle {
    display: flex;
    .decline-button {
      font-family: roboto, arial, sans-serif;
      font-size: 15px;
      font-weight: 400;
      height: 50px;
      color: #1D2D47;
      display: flex;

      letter-spacing: 1.1px;
      align-items: center;
      margin-left: 30px;
      cursor: pointer;
      @media (max-width: $screen-sm-max) {
        margin-left: 30px;
      }
      &:hover {
        color: #f26649;
      }
      &:hover, &:visited, &:focus {
        text-decoration: none;
      }
    }
  }
  .mission-page-links {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    .mission-page {
      cursor: pointer;
      font-size: 18px;
      margin: 10px;
      color: #999999;
      &:hover, &:visited, &:focus {
        text-decoration: none;
      }
    }
    .selected {
      color: #1D2D47;
      font-weight: bolder;
    }
  }
  .see-more-missions {
    text-align: center;
    border-top: 1px solid #eee;
    padding-top: 20px;
    .mission-page {
      font-size: 15px;
      color: #8D95A2;
      font-weight: bold;
    }
    .see-more-missions-icon {
      width: 15px;
    }
  }
}
