.schedule-mission {
  margin-top: 38px;
  font-family: $text-font;
  color: $text-gray;
  @media (max-width: $screen-xs-max) {
    margin-top: 0;
  }
  background-color: white;
  .schedule-mission-title-bar {
    background-color: $brand-turqoise;
    color: white;
    padding: 10px;
    font-size: 22px;
    height: 46px;
    padding-top: 8px;
    text-align: center;
  }
  .schedule-mission-title {
    color: $brand-orange;
    font-size: 20px;
    .small {
      font-size: 50%;
      vertical-align: super;
    }
  }
  .schedule-mission-datetime-container {
    padding: 30px 30px 15px;
    .schedule-mission-datetime-picker {
      .schedule-mission-datetime-picker-label {
        margin-bottom: 8px;
        margin-top: 24px;
      }
    }
    .schedule-mission-datetime-field {
      border-color: $brand-turqoise;
      font-size: 16px;
      height: 40px;
    }
    .schedule-mission-datetime-button {
      border-radius: 0;
      background-color: $brand-turqoise;
      color: white;
      border: none;
      font-size: 20px;
    }
  }

  .schedule-mission-buttons {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    .schedule-mission-button {
      background-color: white;
      @include button;
      &:hover {
        border-color: $brand-orange;
        background-color: $brand-orange;
        color: white;
        text-decoration: none;
      }
      &:disabled {
        background-color: white;
        color: $text-gray;
        border-color: $text-gray;
        cursor: not-allowed;
      }
      border-color: $text-darkblue;
      color: $text-darkblue;
      display: flex;
      justify-content: center;
      margin: 0;
      font-size: 25px;
      letter-spacing: 3px;
      &:first-child {
        margin-right: 20px;
      }
      &.reschedule {
        background-color: $brand-turqoise;
        border-color: $brand-turqoise;
        color: white;
        &:hover {
          background-color: white;
          color: $brand-turqoise;
          text-decoration: none;
        }
        &:disabled {
          background-color: white;
          color: $text-gray;
          border-color: $text-gray;
          cursor: not-allowed;
        }
      }
    }
  }

  .bootstrap-datetimepicker-widget table td.active, .bootstrap-datetimepicker-widget table td.active:hover {
    background-color: $brand-turqoise;
    border-radius: 20px;
  }
  .bootstrap-datetimepicker-widget table td.today:before {
    border-bottom-color: $brand-turqoise;
  }
  .bootstrap-datetimepicker-widget .next, .bootstrap-datetimepicker-widget .prev, .bootstrap-datetimepicker-widget .icon-Xmark {
    color: $brand-turqoise;
  }
  .bootstrap-datetimepicker-widget table td.day {
    font-weight: 400;
  }
  .bootstrap-datetimepicker-widget table td.disabled {
    color: $extra-light-gray;
  }
  table.schedule-mission-table {
    width: 100%;
    background-color: white;
    tr {
      border-bottom: 2px $extra-light-gray solid;
      .schedule-mission-label {
        border-right: 2px $extra-light-gray solid;
        padding-left: 0;
        font-size: 13px;
      }
      .schedule-mission-label-icon {
        font-size: 18px;
      }
      td {
        height: 60px;
        vertical-align: middle;
        padding: 10px;
      }
    }
  }
  .schedule-mission-top {
    display: flex;
    justify-content: center;
    align-items: center;
    .schedule-mission-top-label {
      width: 50%;
      padding-top: 2px;
      padding-bottom: 2px;
      background-color: white;
      margin-bottom: 2px;
      .inner-container {
        //height: 60px;
        padding: 10px;
        border-right: 2px $extra-light-gray solid;
      }
    }
    .schedule-mission-top-content {
      width: 50%;
      padding: 10px;
      background-color: white;
      margin-bottom: 2px;
      //height: 64px;
    }
  }
}
