.weather-forecast {
  padding-top: 10px;
  background-color: white;
}
.weather-forecast-title {
  margin-left: 30px;
  font-size: 18px;
  font-weight: 200;
}
.weather-forecast-table {
  width: 100%;
  margin: 5px 0 0;
}
.weather-forecast-td {
  position: relative;
  vertical-align: top;
  text-align: center;
  background-color: white;
  padding: 15px 0;
  border: 3px solid $extra-light-gray;
  border-right: none;
  border-radius: 0;
  color: $brand-darkblue;
  &:last-child {
    border-right: none;
  }
  &:first-child {
    border-left: none;
  }
  &.current {
    border-color: $brand-turqoise;
    border-left: 3px solid $brand-turqoise;
    border-right: 3px solid $brand-turqoise;
  }
  &.small {
    min-width: 50px;
  }
  &.left-border {
    border-left: 3px solid $extra-light-gray;
  }
}
.weather-icon-canvas {
  padding: 5px 0;
}
.weather-forecast-daily-icon {
  text-align: center;
  padding-top: 5px;
}
.weather-forecast-daily-summary {
  text-align: center;
}
.weather-forecast-label {
  font-size: 10px;
  position: absolute;
  bottom: 2px;
  right: 4px;
  color: $gray;
}
.weather-forecast-links {
  float: right;
  margin-right: 5px;
  font-size: 12px;
  cursor: pointer;
}
.weather-close-button {
  right: 21px;
  position: absolute;
  cursor: pointer;
}
