.ember-modal-overlay {
  height: 100vh;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 50;
  &.translucent {
    background-color: rgba(#808080, .77);
  }
  .ember-modal-dialog {
    border-radius: none ;
    padding: 20px;
    z-index: 50;
    position: fixed;
    &.ember-modal-dialog-in-place {
      position: static;
    }
    .ember-modal-inner-wrap {
      position: relative;
      padding: 40px 50px;
      @media (max-width: $screen-xs-max) {
        padding: 10px;
      }
      background-color: #fff;
      box-shadow: 0 0 10px #222;
      .ember-modal-dialog-close {
        position: absolute;
        top: -12px;
        right: 10px;
        cursor: pointer;
        font-size: 45px;
        font-weight: 100;
        color: $text-gray;
        @media (max-width: $screen-xs-max) {
          font-size: 30px;
          right: 3px;
        }
      }
      .ember-modal-dialog-header {
        text-align: center;
      }
      .ember-modal-dialog-title {
        text-align: center;
        @include headline-font;
        color: $brand-orange;
        font-size: 35px;
        letter-spacing: 2px;

      }
      .ember-modal-dialog-body {
        text-align: center;
        padding: 0 25px 15px;
        p {
          font-size: 15px;
          color: $brand-darkblue;
          padding-bottom: 20px;
          line-height: 20px;
        }
        img {
          height: 200px;
          max-width: 300px;
          margin-bottom: 30px;
          @media (max-width: $screen-xs-max) {
            height: 150px;
            max-width: 200px;
          }
        }
        .confirm-button {
          background-color: white;
          @include button;
          &:hover {
            border-color: $brand-orange;
            background-color: $brand-orange;
            color: white;
            text-decoration: none;
          }
          &:disabled {
            background-color: white;
            color: $text-gray;
            border-color: $text-gray;
            cursor: not-allowed;
          }
          border-color: $text-darkblue;
          color: $text-darkblue;
          margin: 0;
          font-size: 25px;
          letter-spacing: 3px;
          &:first-child {
            margin-right: 20px;
          }
        }
      }
    }
  }
}
