/*
---
name: Inputs
category: Forms
---

Types of buttons:
- input.form-control
- input.form-control.error
- textarea.form-control
- div.inline-input
- div.inline-input.no-edit
- div.inline-textarea


```types.html
<div class='v1'>
<input class='form-control' placeholder="have you smiled today?"/>
<input class='form-control error' placeholder="I haven't smile today"/>
<div class='error'> something is wrong with the value above</div>
<textarea class='form-control' placeholder="have you done any sqats today?"></textarea>
<div class='inline-input'>What is the last book you're read? <i class='fa fa-pencil'></i> </div>
<div class='inline-input no-edit'>What is the last book you're read? <i class='fa fa-pencil'></i> </div>
<div class='inline-textarea'>What are your favorite bands?<i class='fa fa-pencil'></i> </div>
</div>
```
*/
.form {
  h2 {
    margin-bottom: 20px;
    text-align: center;
  }
  .ember-view {
    height: inherit;
  }
}
input.form-control, textarea.form-control {
  border-radius: 0;
}

.form-group-inplace {
  position: relative;

  .field-label {
    position: absolute;
    font-size: 10px;
    color: lightgray;
    left: 14px;
    top: 3px;
  }

  input, textarea {
    padding-top: 16px;
  }

  input {
    min-height: 40px
  }
}
.inline-input, .inline-textarea, .inline-info {
  position: relative;
  padding: 10px;
  border-bottom: 1px dashed lightgray;

  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  transition: all .5s ease;

  &.success {
    @keyframes flashsuccess {
      0%   {background-color: transparent;}
      25%  {background-color: yellow;}
      50%  {background-color: transparent;}
      75%  {background-color: #feffc0;}
      100% {background-color: transparent;}
    }

    background-color: transparent;
    animation-name: flashsuccess;
    animation-duration: 1s;
  }

  .placeholder {
    color: lightgray;
  }

  i {
    position: absolute;
    top: 13px;
    right: 10px;
    color: #000;
    opacity: 0;

    -webkit-transition: opacity .3s ease;
    -moz-transition: opacity .3s ease;
    transition: opacity .3s ease;
  }

  &:hover {
    background-color: #feffc0;

    i {
      opacity: 0.3;
    }
  }
}
.no-edit:hover {
  background-color: $extra-light-gray;
}
.inline-textarea {
  height: max-content;
}

.inline-info {
  margin-bottom: 15px;

  span {
    color: lightgray;
  }

  &:hover {
    cursor: default;
    background-color: transparent;
  }
}
.form-login {
  button {
    width: 100%;
  }
}
