.dashboard-header-wrap {
  @include bottom-shadow;
  z-index: 3;
  position: relative;
  .dashboard-header {
    height: 75px;
    background: white;
    @include headline-font;
    font-size: 16px;
    text-transform: uppercase;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    text-align: center;
    @media (max-width: $screen-xs-max) {
      font-size: 12px;
    }

    .header-item {
      display: flex;
      flex: 1;
      .count {
        font-size: 40px;
        @media (max-width: $screen-xs-max) {
          font-size: 30px;
        }
      }
      .dollarsign {
        font-size: 32px;
      }
    }
    .header-item.divider {
      border-right: 1px solid black;
    }
    .header-icon {
      margin-right: 10px;
      font-size: 28px;
      color: $brand-turqoise;
      @media (max-width: $screen-xs-max) {
        margin-right: 5px;
        font-size: 20px;
        margin-left: 2px;
      }
    }
    .header-icon-2 {
      margin-right: 10px;
      font-size: 38px;
      color: $brand-turqoise;
      @media (max-width: $screen-xs-max) {
        margin-right: 5px;
        font-size: 25px;
        margin-left: 5px;
      }
    }
    .header-container {
      display: flex;
      flex: 1;
      justify-content: center;
      align-items: center;
      .small-text {
        margin-top: 21px;
        margin-left: 10px;
        line-height: 1;
        text-align: start;
        align-self: flex-start;
        @media (max-width: $screen-xs-max) {
          min-height: 36px;
          margin-left: 5px;
          margin-top: 25px;
        }
      }
    }
  }
}
