.active-missions-container {
  position: absolute;
  right: 0;
  display: flex;
  flex-direction: column;
  z-index: 1;
  right: 0;
  align-items: flex-end;
  top: 0;
  bottom: 0;
  @media (max-width: $screen-xs-max) {
    width: 100%;
  }
  &.shrink {
    height: 32px;
    width: 167px;
    bottom: none;
  }
  &.shrink {
    .icon-Arrow2_up {
      display: none;
    }
    .icon-Arrow2_down {
      display: inline;
    }
  }
  .icon-Arrow2_up {
    display: inline;
  }
  .icon-Arrow2_down {
    display: none;
  }
  .active-missions {
    background-color: $brand-darkblue;
    padding: 2px 8px 2px 28px;
    color: $brand-mint;
    font-size: 20px;
    @include headline-font;
    cursor: pointer;
    display: flex;
    .arrow {
      margin-top: 3px;
    }

  }

  .mission-wrapper {
    transition: height 0.5s ease;
    overflow:scroll;
    position: absolute;
    top: 32px;
    bottom: 0;
    right: 0;
    @media (max-width: $screen-xs-max) {
      display: none;
      width: 100%;
    }
  }
  .scroll-wrapper {
    position: relative;
    padding-left: 30px;
    @media (max-width: $screen-xs-max) {
      padding: 0;
    }

  }
  .down-arrow {
    position: absolute;
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    right: 24px;
    top: 0;
  }

  .mission-panel-selected {
    opacity: 1 !important;
    -webkit-box-shadow: -8px 10px 50px -5px rgba(0, 0, 0, 0.45);
    -moz-box-shadow: -8px 10px 50px -5px rgba(0, 0, 0, 0.45);
    box-shadow: -8px 10px 50px -5px rgba(0, 0, 0, 0.45);
    z-index: 2;
  }

  .available-mission-panel {
    border-top: 4px solid $brand-yellow;
    .down-arrow {
      border-top: 8px solid $brand-yellow;
    }
  }

  .client-mission-panel {
    border-top: 4px solid $brand-turqoise;
    .down-arrow {
      border-top: 8px solid $brand-turqoise;
    }
    .icon-Weather {
      position: relative;
      top: 4px;
      left: 10px;
      font-size: 25px;
      cursor: pointer;
      color: $brand-turqoise;
    }
    .weather-forecast-table {
      font-size: 12px;
    }
  }
  .no-active-mission-panel, .available-mission-panel, .client-mission-panel {
    float: right;
    width: 350px;
    background-color: white;
    padding: 12px 12px 20px 20px;
    color: $text-darkblue;
    position: relative;
    opacity: 0.9;
    cursor: pointer;
    .details {
      color: #999;
    }
    .icon-Pin, .icon-Calendar, .icon-Checkmark_c {
      float: left;
      font-size: 18px;
      margin-right: 5px;
    }
    .icon-Calendar {
      margin-top: 5px;
    }
    @media (max-width: $screen-xs-max) {
      width: 100%;
    }
  }

  .no-active-mission-panel {
    display: flex;
    flex-direction: column;
    border-top: 4px solid $brand-orange;
  }
  .no-active-mission-panel-title {
    display: flex;
    justify-content: center;
    font-size: 28px;
    color: $brand-orange;
    @include headline-font;
    text-transform: uppercase;
  }
  .no-active-mission-panel-copy {
    font-size: 16px;
    display: flex;
    justify-content: center;
    text-align: center;
  }
  .no-active-mission-panel-img {
    margin-top: -40px;
    margin-bottom: -40px;
  }
  .price {
    position: absolute;
    right: 20px;
    top: 25px;
    font-size: 32px;
    color: $brand-orange;
    @include headline-font;
    .dollarsign {
      font-size: 26px;
    }
  }

  .mission-id {
    @include headline-font;
    font-size: 26px;
  }

  .button-panel {
    display: flex;
    flex-direction: row;
    justify-content: center;
    a {
      color: $text-darkblue;
    }
    .button {
      @include button;
      &:hover {
        background-color: $text-darkblue;
        color: white;
        text-decoration: none;
      }
    }
    .status-button {
      margin-top: 20px;
      width: 161px;
      margin-left: -23px;
      @media (max-width: $screen-xs-max) {
        width: 150px;
        margin-left: -3px;
      }
      .status-icon-text {
        font-size: 12px;
        @media (max-width: $screen-xs-max) {
          line-height: 21px;
          font-size: 11px;
        }
      }

      padding-left: 15px;
    }
  }

  .address {
    color: #999;
  }
  .address-wrapper {
    margin-left: 20px;
    width: 240px;
  }
  .date-wrapper {
    margin-left: 20px;
    width: 240px;
    margin-top: 5px;
  }

  .getty-mission-panel {
    float: right;
    width: 350px;
    background-image: url("/assets/images/getty/Getty_MissionCard.png");
    background-size: 195px;
    background-position: right 60px;
    background-repeat: no-repeat;
    color: $brand-darkblue;
    background-color: white;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border-top: 4px solid $brand-orange;
    .headline {
      @include headline-font;
      font-size: 26px;
      font-weight: 500;
    }
    .down-arrow {
      border-top: 8px solid $brand-orange;
      top: 4px;
    }
    .text {
      color: #999;
      width: 150px;
    }
    .divider {
      width: 40px;
      border: 1px solid $brand-orange;
      margin: 2px 0 4px;
    }
    a {
      margin-top: 10px;
      font-size: 24px;
      height: 34px !important;
      padding: 4px 10px;
    }
    @media (max-width: $screen-xs-max) {
      width: 100%;
      background-size: 165px;
      background-position: right 90px;
    }
  }
  .pano-mission-panel {
    float: right;
    width: 350px;
    border-top: 4px solid $brand-orange;
    background-color: white;
    padding: 12px 12px 20px 20px;
    color: $text-darkblue;
    position: relative;
    opacity: 0.9;
    cursor: pointer;
    .down-arrow {
      border-top: 8px solid $brand-orange;
    }
    .icon-Star_b {
      position: absolute;
      right: 20px;
      top: 20px;
      font-size: 24px;
      color: $brand-orange;
    }
    .icon-Pin {
      float: left;
      color: #999;
      font-size: 18px;
    }
    @media (max-width: $screen-xs-max) {
      width: 100%;
    }
  }
}
.lowered {
  top: 200px !important;
  &.client {
    top: 235px !important;
  }
  &.pano-commercial {
    top: 262px !important;
  }
  &.pano-residential {
    top: 289px !important;
  }
  @media (max-width: $screen-sm-max) {
    top: 300px !important;
    &.client {
      top: 335px !important;
    }
    &.pano-commercial {
      top: 362px !important;
    }
    &.pano-residential {
      top: 389px !important;
    }
  }
  @media (max-width: $screen-xs-max) {
    top: 298px !important;
    &.client {
      top: 333px !important;
    }
    &.pano-commercial {
      top: 360px !important;
    }
    &.pano-residential {
      top: 387px !important;
    }
  }
}
.should-lower {
  transition: top 0.5s ease;
}
