.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2;
  display: none;
}
.profile {
  background-color: #f1f6f2;
  top: 0px;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  .row {
    background-color: #f1f6f2;
  }
  .profile-component {
    margin-bottom: 30px;

    .missing-info {
      border: 2px solid $brand-orange;
      margin: 0;
      padding: 0 15px;
    }
  }
  .form-group {
    position: relative;
  }
  color: $text-darkblue;
  height: 100%;
  padding-bottom: 50px;
  @media (max-width: $screen-xs-max) {
    font-size: 12px;
  }
  .header {
    color: white;
    height: 300px;
    background-image: url("/assets/images/v2/profile_background.png");
    background-repeat: repeat;
    text-align: center;
    padding-top: 65px;
    .pilot-img {
      width: 100px;
    }
    .name {
      font-size: 18px;
      padding-top: 15px;
      text-transform: capitalize;
      color: $brand-mint;
    }
  }
  .content {
    @media (max-width: $screen-xs-max) {
      padding: 0 20px 20px;
    }
  }
  .licenses-row, .drones-row, .availability-row, .equipment-row, .devices-row {
    .content {
      padding-top: 10px;
    }
  }
  .focused {
    position: relative;
    z-index: 3;
    background: #f1f6f2;
  }
  .inline-input, .page-title, .field-name, .field-spacer {
    border-bottom: 1px solid #a6b0c1;
  }
  .field-name {
    padding-bottom: 8px;
    min-width: 90px;
    @media (max-width: $screen-xs-max) {
      min-width: 77px;
    }
    &.wide {
      min-width: 150px;
    }
    &.narrow {
      min-width: 65px;
    }
    color: $text-darkblue;
  }
  .field-spacer {
    padding-bottom: 3px;
    padding-left: 12px;
    &::after {
      content: " |";
      font-size: 22px;
      color: #a6b0c1;
      font-weight: 100;
    }
    &.narrow {
      padding-left: 7px;
    }
  }
  .page-title {
    padding: 15px 15px;
    font-size: 21px;
    font-weight: 500 !important;
    margin-bottom: 0;
    letter-spacing: 0.5px;
    border-bottom: 0;
    @media (max-width: $screen-xs-max) {
      font-weight: 18px;
      padding-top: 0;
    }
  }
  .header-badge {
    position: absolute;
    top: 4px;
    display: inline-block;
    margin-left: 15px;
    background: $brand-orange;
    font-size: 10px;
    color: white;
    border-radius: 2px;
    padding: 1px 4px;
    padding-left: 5px;

    @media (min-width: $screen-sm-min) {
      top: 39px;
    }
  }
  .header-badge:hover {
    cursor: pointer;
  }
  .inline-input {
    color: $text-gray;
    cursor: pointer;
    &.no-edit {
      cursor: not-allowed;
      padding: 7px 18px;
      @media (max-width: $screen-xs-max) {
        padding: 7px 8px;
      }
    }
  }
  .section-header {
    background-color: #d6dcdc;
    padding: 10px 15px 9px;
    font-size: 15px;
    font-weight: 400;
    letter-spacing: 0.5px;
  }
  .secion-description {
    padding: 10px 15px 9px;
    font-size: 15px;
    font-style: italic;
    border-bottom: solid 1px #a6b0c1;
  }
  .item-row {
    display: flex;
    align-items: flex-end;
    margin: 0 15px;
    padding: 0;
    min-height: 40px;
    > div {
      flex-grow: 1;
      margin: 0;
      input[type=text], input[type=password] {
        background-color: #f1f6f2;
        color: #555;
        cursor: pointer;
        font-size: 14px;
        -webkit-box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
        box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
        &:focus, &:hover {
          background-color: #feffc0;
        }
      }
    }

    li:hover {
      background-color: #eee!important;
    }
    .availability {
      border-bottom: 1px solid #a6b0c1;
      line-height: 2;
      padding-bottom: 4px;
      padding-left: 4px;
    }
    .field-value {
      position: relative;
      width: 100%;
      padding: 4px 0 7px 15px;
      border-bottom: 1px solid $text-darkblue;
      color: $text-gray;
      .field-arrow {
        position: absolute;
        right: 10px;
        bottom: 5px;
        font-size: 20px;
        color: $text-gray;
        cursor: pointer;
      }
    }
    .field-value-empty {
      font-style: italic;
    }
  }
  .drone-system, .license, .equipment, .devices {
    padding: 0;
    position: relative;
    .field-value, .field-name, .field-spacer {
      border-bottom: none;
      &.new {
        color: $text-gray;
      }
    }
    & > .field-value {
      cursor: pointer;
    }
    .drone-system-item, .license-item, .equipment-item, .device-item {
      .drone-system-name, .license-name, .equipment-name, .device-name {
        position: relative;
        padding: 0 15px 7px;
        .delete-me {
          position: absolute;
          right: 10px;
          bottom: 10px;
          font-size: 12px;
          color: $text-gray;
          cursor: pointer;

        }
      }
    }
    .drone-system-item {
      padding: 0;
    }
    ul.drone-dropdown, ul.camera-dropdown, ul.license-dropdown, ul.equipment-dropdown, ul.device-dropdown {
      display: none;
      z-index: 10;
      position: absolute;
      margin: 0;
      padding: 0;
      list-style: none;
      overflow-y: scroll;
      box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
      max-height: 400px;
      li {
        margin: 0;
        padding: 10px 25px;
        background-color: white;
        border-bottom: 1px solid $light-gray;
        cursor: pointer;
      }
    }
    ul.license-dropdown, ul.drone-dropdown, ul.equipment-dropdown, ul.device-dropdown {
      top: 30px;
      left: 115px;
    }
    ul.camera-dropdown {
      left: 185px;
    }
    .btm-border-line {
      border-bottom: solid 1px #a6b0c1;
    }
    .cameras-container, .license-number {
      background-color: #e6ebe9;
      position: relative;
      padding: 0;
      border-bottom: solid 1px #a6b0c1;
      .top-arrow {
        position: absolute;
        top: 0;
        left: 18px;
        width: 0;
        height: 0;
        border-left: 7px solid transparent;
        border-right: 7px solid transparent;
        border-top: 8px solid #f1f6f2;
      }
      .cameras-section, .license-number-section {
        margin: 0 15px 0 120px;
        @media (max-width: $screen-xs-max) {
          margin-left: 6px;
        }
        font-size: 12px;
        cursor: pointer;
        display: flex;
        .cameras-section-title, .licence-number-section-title {
          margin-right: 10px;
          justify-content: center;
          display: flex;
          flex-direction: column;
          min-height: 34px;
        }
        .cameras-section-pills {
          margin: 2px 0;
          .camera-pill {
            font-size: 12px;
            font-weight: 200;
            border-radius: 15px;
            background-color: white;
            color: #888;
            padding: 3px 10px 2px;
            margin: 2px 0px;
            .delete-me {
              margin-left: 5px;
            }
          }
        }
      }
      .form-group {
        display: inline-block;
        margin: 0;
        .form-control {
          border-bottom: none;
          background-color: #e6ebe9;
        }
      }
      .field-arrow {
        position: absolute;
        right: 7px;
        bottom: 5px;
        font-size: 20px;
        color: $text-gray;
        cursor: pointer;
      }
    }
  }

  .form-control {
    border-bottom: 1px solid #a6b0c1;
    border-top: none;
    border-left: none;
    border-right: none;
    padding-left: 18px;
    @media (max-width: $screen-xs-max) {
      padding-left: 8px;
    }
    padding-bottom: 7px;
  }
  .full-row {
    line-height: 1.5;
    display: flex;
    align-items: center;
    margin: 0 15px;
    padding: 15px 0 5px;
    &.line {
      border-bottom: 1px solid #a6b0c1;
    }
  }
  .payment-row {
    padding-top: 7px;
  }
  .error {
    position: absolute;
    color: $brand-orange;
    font-size: 12px;
    top: -2px;
    left: 12px;
    width: 100%;
  }
  .saved {
    position: absolute;
    color: $brand-darkblue;
    font-size: 10px;
    top: -2px;
    right: 2px;
  }
  .roundCheckbox {
    position: relative;
    display: inline-block;
    width: 25px;
    height: 25px;
    margin: 0px 5px 0 14px;
    .ember-view {
      display: inherit;
    }
    img {
      cursor: pointer;
    }
  }
}
.opt-in-training {
  display: flex;
  width: 100%;
  background: white;
  justify-content: center;
  align-items: center;
  height: 128px;
  @media (max-width: $screen-sm-max) {
    height: 140px;
  }
  @media (max-width: $screen-xs-max) {
    flex-direction: column;
    height: auto;
    align-items: flex-start;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
  }
  img {
    width: 86px;
    margin-right: 40px;
  }
  .interested-in-training {
    width: 620px;
    font-size: 14px;
    font-weight: 200;
    @media (max-width: $screen-sm-max) {
      width: 350px;
    }
    @media (max-width: $screen-xs-max) {
      margin-top: 20px;
      margin-bottom: 15px;
      margin-left: 25px;
      margin-right: 25px;
      width: auto;
    }
    h2 {
      font-size: 18px;
      font-weight: 400 !important;
      margin-top: 0;
      @media (max-width: $screen-xs-max) {
        font-size: 16px;
      }
    }
  }
}
