.pano-mission {
  background: $extra-light-gray;
  h3 {
    color: $brand-darkblue;
    margin-top: 30px;
    @media (max-width: $screen-xs-max) {
      font-size: 18px;
    }
  }
  .mission-content {
    background: $extra-light-gray;
    @media (min-width: $screen-sm-min) {
      padding: 10px 50px;
    }
  }
  .button {
    @include button;
    &:hover {
      border-color: $brand-orange;
      background-color: $brand-orange;
      color: white;
      text-decoration: none;
    }
    margin-top: 0;
    border-color: $text-darkblue;
    color: $text-darkblue;
    display: flex;
    justify-content: center;
  }
  .header {
    background-color: white;
    padding: 15px 36px;
    display: flex;
    justify-content: space-between;
    .title {
      @include headline-font;
      font-size: 30px;
      .id {
        color: $text-turqoise;
      }
    }
    .address {
      color: #888;
    }
  }
  .map-container {
    height: 500px;
    @media (max-width: $screen-xs-max) {
      height: 200px;
    }
    #map-canvas {
      height: 500px;
      @media (max-width: $screen-xs-max) {
        height: 200px;
      }
    }
    .map-overlay {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 50px;
      height: 50px;
      font-size: 50px;
      background: grey;
      cursor: pointer;
      [class^="icon-"] {
        position: relative;
        top: -5px;
        color: white;
      }
      .weather {
        margin-left: 2px;
      }
    }
    iframe {
      width: 100%;
      height: 500px;
      border: none;
      @media (max-width: $screen-xs-max) {
        height: 350px;
      }
    }
  }
  .instructions-panel {
    margin-top: 15px;
    margin-bottom: 15px;
    .info-graphic {
      display: flex;
      padding: 0;
      @media (max-width: $screen-xs-max) {
        flex-direction: column;
      }
      .item-wrapper {
        padding: 0 15px;
      }
      .item, .last-item {
        box-shadow: 0 0 15px rgba(178, 178, 178, 0.8);
        width: 100%;
        background: white;
        height: 100%;
        display: flex;
        padding: 10px 25px 15px 25px;
        flex-direction: column;
        align-items: center;
        text-align: center;
        @media (max-width: $screen-xs-max) {
          flex-direction: row;
          margin-bottom: 15px;
        }
        @media (max-width: 520px) {
          flex-direction: column;
          margin-bottom: 15px;
          height: 340px;
        }
        .title {
          color: $text-turqoise;
          @include headline-font;
          font-size: 26px;
          @media (max-width: $screen-xs-max) {
            margin-top: 0;
            width: 100%;
          }
        }
        .info-img {
          width: 150px;
        }
        .num {
          color: $text-turqoise;
          align-self: flex-end;
        }
      }
      .item {
        margin-right: 15px;
      }
    }
    &.desktop {
      color: $brand-orange;
      background-color: inherit;
      height: 100px;
      margin-bottom: 0;
    }
    .desktop-icon-text {
      display: table-cell;
    }
    .desktop-icon {
      display: table-cell;
      padding-right: 10px;
      font-size: 20px
    }
  }
  .weather {
    margin: 25px -15px;
  }
  .uploads {
    margin-top: 15px;
    margin-bottom: 15px;
    .directions {
      width: 50%;
      .orange {
        color: $brand-orange;
        font-size: 18px;
      }
    }
  }
  .cover {
    margin-top: 25px;
  }
  .submit-row {
    margin: 20px 0;
  }
  .submit-button {
    @include button;
    &:hover {
      border-color: $text-darkblue;
      background-color: $text-darkblue;
      color: white;
      text-decoration: none;
    }
    border-color: $brand-orange;
    background-color: $brand-orange;
    color: white;
    display: flex;
    justify-content: center;
  }
  .complete {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    height: 400px;
    border: 8px solid white;
    background-color: $brand-mint;
    margin: 36px;
    .orange {
      @include headline-font;
      color: $brand-orange;
      font-size: 36px;
    }
    img {
      width: 300px;
    }
  }
  .pano-toggle {
    position: absolute;
    right: 0;
    top: 0;
    font-size: 50px;
    background: #333;
    opacity: 0.8;
    padding: 5px;
    cursor: pointer;
    color: white;
  }
  .final-instructions {
    margin-top: 10px;
    font-size: 12px;
    text-align: right;
  }
}
