.profile-tooltip-prompt {
  background: white;

  .left-arrow {
    @media (max-width: $screen-sm-max) {
      display: none;
    }
    
    @media (min-width: $screen-md-min) {
      top: 35%;
      left: -12px;
      border-width: 14px 17.3px 14px 0;
      border-color: transparent white transparent transparent;
      position: absolute;
      width: 0;
      height: 0;
      border-style: solid;
    }
  }
}
