.map-container-pilot-dashboard {
  width: 70vw;
  height: 70vh;
  z-index: 0;
  // @media (max-width: $screen-xs-max) {
  //   width: 80vw;
  //   height: 80vh;
  // }
}

.gm-style-iw {
  width: 575px!important;
  top: 15px !important;
  left: 0 !important;
  background-color: #fff;
  box-shadow: 0 1px 6px rgba(178, 178, 178, 0.6);
  border: none;
  border-image: none;
  border-radius: 6px;
  @media (max-width: $screen-xs-max) {
    width: 270px !important;
    div:first-of-type {
      max-width: none!important;
    }
  }

}
.info-window-panel {
  position: relative;
  width: 550px;
  height: 130px;
  @media (max-width: $screen-xs-max) {
    width: 250px !important;
    height: 200px;
  }
  .turquoise-button {
    height: 45px;
    @media (max-width: $screen-xs-max) {
      height: 35px;
      font-size: 13px;
    }
  }

  .mission-wide-col {
    width: 320px;
    margin-left: 30px;
    @media (max-width: $screen-xs-max) {
      width: auto;
      margin-left: 20px;
    }

  }
  .second-row {
    display: flex;
    @media (max-width: $screen-xs-max) {
      display: block;
    }
    .address-and-pin {
      display: flex;
    }
    .icon {
      width: 20px;
      height: 30px;
      margin-right: 15px;
      @media (max-width: $screen-xs-max) {
        width: 20px;
        margin-right: 15px;
        height: 30px;
      }
    }
    .available-mission-pin {
      background: url("/assets/images/pilot/available_solid_pin.svg");
      background-repeat: no-repeat;
      @media (max-width: $screen-xs-max) {
        background-size: contain;
      }
    }
    .client-mission-pin {
      background: url("/assets/images/pilot/client_solid_pin.svg");
      background-repeat: no-repeat;
    }
    .getty-mission-pin {
      background: url("/assets/images/pilot/getty_solid_pin.svg");
      background-repeat: no-repeat;
    }
    .address {
      width: 200px;
      font-family: roboto;
      font-size: 16px;
      text-align: left;
      font-weight: 400;
      @media (max-width: $screen-xs-max) and (orientation: landscape){
        font-size: 13px;
        line-height: 18px;
      }
      @media (max-width: $screen-xs-max) and (orientation: portrait){
        font-size: 14px;
        line-height: 22px;
      }

      .mission-schedule {
        margin-top: 5px;
      }
      .days {
        margin-top: 10px;
      }
      .time {
        font-size: 12px;
        color: #777;
      }
    }
  }
  .buttons {
    @media (max-width: $screen-xs-max) {
      margin-left: 20px;
      margin-top: 10px;
    }
    display: flex;
    .decline-mission-button {
      margin-left: 30px;
      font-family: roboto;
      font-weight: 500;
      font-size: 15px;
      color: #1D2D47;
      display: flex;
      height: 50px;
      align-items: center;
      cursor: pointer;
      @media (max-width: $screen-xs-max) {
        height: 35px;
        font-size: 13px;
      }
    }
  }

  .header {
    display: flex;
    margin-top: 25px;
    height: 50px;
    @media (max-width: $screen-xs-max) {
      height: 25px;
      margin-top: 20px;
      margin-bottom: 5px;
      justify-content: space-between;
    }
    @media (max-width: $screen-xs-max) and (orientation: landscape){
      margin-top: 14px;
    }
    .mission-title {
      width: 100%;
      text-align: left;
    }
    .mission-title, .mission-payout {
      font-family: roboto;
      font-weight: 500;
      color: #1F2B45;
      font-size: 22px;
      @media (max-width: $screen-xs-max) {
        font-size: 16px;
      }
    }
    .mission-payout {
      @media (max-width: $screen-xs-max) {
        font-size: 18px;
      }
    }
    .mission-schedule {
      width: 217px;
      text-align: left;
      @media (max-width: $screen-xs-max) {
        width: 100px;
      }

      font-family: roboto;
      .day {
        color: #1D2D47;
        font-size: 18px;
        font-weight: 400;
        @media (max-width: $screen-xs-max) {
          font-size: 15px;
        }

      }
      .time {
        color: #777;
        margin-top: 10px;
      }
    }
  }
}
