.upload-assets-other-app {
  background-color: white;
  box-shadow: 0 3px 6px rgba(0,0,0,0.3);
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  padding: 60px;
  margin-bottom: 150px;
  .other-app-pic {
    width: 100%;
    text-align: center;
    margin-bottom: 35px;
    img {
      margin: auto;
      width: 340px;
      height: auto;
    }
    .third-party-app {
      height: 220px;
    }
  }
  .title {
    font-size: 24px;
    color: #F26649;
    margin-bottom: 10px;
  }
  .description {
    margin-bottom: 20px;
    max-width: 620px;
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    a {
      text-transform: underline;
      color: #3FC0C2;
      cursor: pointer;
    }
    ol, ul {
      text-align: left;
    }
    li {
      margin-bottom: 5px;
    }
  }
  .small-description {
    margin-top: 20px;
    font-size: 14px;
    color: #8D95A2;
  }
  .generating-key {
    margin-top: 15px;
    color: #1D2D47;
    font-size: 14px;
    font-weight: 200;
  }
  .duck-file {
    display: flex;
    color: #149B9E;
    font-size: 16px;
    height: 40px;
    align-items: center;
    cursor: pointer;
    margin-bottom: 10px;
    width: 265px;
    &:hover {
      text-decoration: underline;
    }
    .credential-icon {
      width: auto;
      height: 35px;

    }
  }
  .cyberduck-access {
    .cyberduck-title {
      color: #8D95A2;
      font-size:13px;
      font-weight: 400;
    }
    .copy-text {
      margin-top: 5px;
      input {
        height: 40px;
        width: 316px;
        background: rgba(197, 201, 208, .2);
        border: none;
        color: #1D2D47;
        font-size: 16px;
        font-weight: 200;
        padding-left: 20px;
        &:focus {
          outline: 0px;
        }
      }
      button {
        height: 40px;
        width: 60px;
        margin-left: -3px;
        padding: 0;
        background: white;
        border: 2px #F3F4F5 solid;
        &:focus {
          outline: 0px;
        }
        p {
          color: #149B9E;
          font-size: 14px;
          font-weight: 400;
          margin: 0;
          margin-left: 5px;
          margin-right: 5px;
        }
        .copy-link {
          height: 40px;
          width: 28px;
          margin-left: 2px;
          margin-right: 2px;
        }

      }
    }
  }
  .done-uploading-text {
    margin-top: 20px;
    margin-bottom: 20px;
    color: #1D2D47;
    font-size: 16px;
    font-weight: 200;
  }
}
