.pilot-stats {
  background-color: white;
  padding: 5px;
  @media (min-width: $screen-md-min) {
    padding: 10px;
  }
  border-left: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
  &:last-child {
    border-right: 1px solid #e0e0e0;
  }
  @media (min-width: $screen-sm-min) {
    border-bottom: none;
  }
  .pilot-stats-number {
    font-size: 30px;
    @media (min-width: $screen-md-min) {
      font-size: 50px;
    }
    font-weight: 100;
  }
  .pilot-stats-label {
    font-size: 12px;
    @media (min-width: $screen-md-min) {
      font-size: 16px;
    }
    font-weight: 200;
    display: inline-block;
    text-align: left;
    margin-left: 20px;
  }
}
