.pilot-registration {
  background: url("/assets/images/v2/RegisterPage_Background.jpeg") no-repeat center center fixed;
  background-size: cover;
  top: 0;
  bottom: 0;
  position: absolute;
  width: 100%;
  .container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .row {
      color: #aaa;
      .container-content {
        background-color: rgba(0, 0, 0, 0.3);
        padding: 0 15px;
        .page-header {
          display: flex;
          justify-content: center;
          border: none;
          flex-direction: column;
          align-items: center;
          .divider {
            margin: 10px;
            width: 50px;
            border-top: 1px solid $brand-orange;
          }
        }
        .close-button {
          color: white;
          position: absolute;
          right: 0;
          margin: 10px;
        }
        .close-button:hover {
          text-decoration: none;
        }
        .error {
          position: absolute;
          top: 0px;
          left: 20px;
          font-size: 12px;
          color: $brand-orange;
          &.term {
            position: relative;
            top: auto;
            left: auto;
          }
        }
        .input-lg {
          background-color: transparent;
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: 1px solid $brand-orange;
          padding-left: 5px;
        }
        .form-group {
          display: flex;
          align-items: center;
          flex-direction: column;
          .register-button {
            @include button;
            background-color: transparent;
            display: flex;
            justify-content: center;
            padding: 3px 0;
            margin-bottom: 30px;
            color: $brand-turqoise;
            border: 1px solid $brand-turqoise;
          }
          .register-button.disabled {
            opacity: 0.5;
          }
          .register-button:hover {
            background-color: $brand-turqoise;
            color: black;
            text-decoration: none;
          }
          .address-field {
            align-items: stretch;
          }
        }
      }
    }
  }
}
