.profile-tooltip {
  position: absolute;
  background: #CCEBE8;
  width: 265px;
  font-family: roboto, arial, sans-serif;
  padding: 30px;
  z-index: 4;
  display: none;

  @media (min-width: $screen-md-min) {
    width: 335px;
    padding: 20px 35px;
  }

  .left-arrow {
    position: absolute;
    top: 98%;
    left: 45%;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 17.3px 14px 0 14px;
    border-color: #CCEBE8 transparent transparent transparent;

    @media (min-width: $screen-md-min) {
      top: 35%;
      left: -12px;
      border-width: 14px 17.3px 14px 0;
      border-color: transparent #CCEBE8 transparent transparent;
    }
  }

  .cross {
    position: absolute;
    top: -1px;
    right: -3px;
    margin-top: 10px;
    margin-right: 10px;
    font-size: 16px;
    color: #777;
  }

  .cross:hover {
    cursor: pointer;
  }

  .content {
    border-radius: 2px;
  }

  .title {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 5px;
  }

  .text {
    font-size: 14px;
    font-weight: 300;
  }

  .footer {
    margin-top: 15px;

    img {
      width: 12px;
      margin-bottom: 1px;
    }

    a {
      color: $brand-orange;
    }

    a:hover {
      cursor: pointer;
    }
  }

  .pills {
    letter-spacing: 3px;
    font-size: 7px;
    color: white;
    position: absolute;
    bottom: 16px;
    right: 14px;

    .selected{
      color: $brand-orange;
    }
  }
}
