.map-filter {
  position: absolute;
  z-index: 1;
  background-color: white;
  margin: 3px;
  color: $text-gray;
  font-size: 20px;
}
.map-filter-search {
  display: none;
  position: absolute;
  z-index: 2;
  margin-top: 3px;
  margin-left: 38px;
  background-color: white;
  padding: 5px;
  top: 0;
  -webkit-box-shadow: 0px 10px 14px -5px rgba(0, 0, 0, 0.45);
  -moz-box-shadow: 0px 10px 14px -5px rgba(0, 0, 0, 0.45);
  box-shadow: 0px 10px 14px -5px rgba(0, 0, 0, 0.45);
  & input {
    color: $text-gray;
    border: 0;
    border-bottom: 2px solid #777;
    width: 400px;
    @media (max-width: $screen-xs-max) {
      width: 250px;
    }
  }
}

.map-filter-right-arrow {
  position: absolute;
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  left: -6px;
  top: 7px;
  border-right: 6px solid white;
}

.map-filter-missions {
  display: none;
  position: absolute;
  z-index: 2;
  left: 38px;
  padding: 5px;
  background-color: white;
  color: $text-gray;
  -webkit-box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.45);
  -moz-box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.45);
  box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.45);
  &.client {
    top: 35px;
  }
  &.pano-commercial {
    top: 62px;
  }
  &.pano-residential {
    top: 89px;
  }
}

.map-filter-button {
  width: 50px;
  text-align: center;
  font-size: 10px;
  line-height: 10px;
  & span {
    font-size: 20px;
    display: block;
    margin-bottom: 3px;
    color: $text-gray;
  }
  margin: 0 10px;
}

.map-filter-mission {
  cursor: pointer;
  float:left;
  position: relative;
  &.right-border {
    border-right: 1px solid $text-gray;
  }
}
.client-mission-icon-color {
  color: $brand-turqoise !important;
}
.client-mission-available-icon-color {
  color: $brand-yellow !important;
}
.pano-icon-color {
  color: $brand-orange !important;
}

.icon-filter {
  margin: 8px 5px;
  cursor: pointer;
}
