.pilot-dashboard {
  color: #1F2B45;
  margin-top: 50px;
  @media (max-width: $screen-xs-max) {
    margin-top: 0;
  }
  .weather {
    font-size: 30px;
    font-family: roboto;
    font-weight: lighter;
    display: flex;
    justify-content: center;
    align-items: center;
    div {
      padding-top: 5px;
    }
    @media (max-width: $screen-xs-max) {
      font-size: 20px;

    }
  }
  .temperature:after {
    content: "\00b0";
  }
  h1 {
    font-family: roboto;
    font-weight: lighter;
    font-size: 40px;
    @media (max-width: $screen-xs-max) {
      font-size: 22px;
      margin-bottom: 5px;
      margin-top: 15px;
    }
  }
  .small-quote {
    font-style: italic;
    font-size: 15px;
    @media (max-width: $screen-xs-max) {
      font-size: 14px;
    }
  }
  .pilot-dashboard-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding-bottom: 50px;
    width: 100%;
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
    @media (max-width: $screen-xs-max) {
      padding-bottom: 25px;
    }
  }
  .pilot-stats-container {
    margin-top: 43px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    @media (max-width: $screen-xs-max) {
      margin-top: 30px;
    }
    h1 {
      font-size: 38px;
      @media (max-width: $screen-xs-max) {
        font-size: 27px;
        margin-top: 10px;
        .dollarsign {
          font-size: 20px;
        }
      }
    }
    .pilot-stats-chunk {
      display: flex;
    }
    .pilot-stat-box {
      display: flex;
      flex-direction: column;
      align-items: center;
      border-radius: 4px;
      width: 250px;
      margin-left: 20px;
      margin-right: 20px;
      margin-bottom: 20px;
      box-shadow: 0 3px 8px rgba(0,0,0,0.16);
      @media (max-width: $screen-xs-max) {
        width: 105px;
        height: 85px;
        margin-left: 5px;
        margin-right: 5px;
      }
      .small-text {
        font-size: 14px;
        font-family: roboto;
        font-weight: lighter;
        margin-bottom: 25px;
        @media (max-width: $screen-xs-max) {
          width: 40px;
          text-align: center;
          font-size: 11px;
          font-weight: normal;
          display: flex;
          justify-content: center;
        }
      }
    }
  }
  .client-missions {
    .no-missions {
      .turquoise-button {
        width: 300px;
        @media(max-width: $screen-xs-max) {
          margin-left: auto;
          margin-right: auto;
        }
      }
    }
    .steps-text-header {
      margin-top: 50px;
    }
    .learn-more {
      font-size: 16px;
      a {
        color: #149B9E;
      }
      @media (max-width: $screen-xs-max) {
        font-size: 15px;
      }
    }
    .steps-icons {
      display: flex;
      margin-top: 10px;
      justify-content: center;
      align-items: center;
      @media (max-width: $screen-xs-max) {
        flex-direction: column;
        margin-top: 20px;
        margin-bottom: 20px;
      }
      .step-icon-and-text {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 220px;
        @media(max-width: $screen-xs-max) {
          flex-direction: row;
          width: auto;
        }
        img {
          width: 220px;
          @media(max-width: $screen-xs-max) {
            width: 80px;
          }

        }
        p {
          width: 175px;
          text-align: center;
          margin-top: 20px;
          font-size: 16px;
          color: #8D95A2;
          margin-bottom: 0px;
          height: 80px;
          @media(max-width: $screen-xs-max) {
            margin-top: 0;
            margin-bottom: 0;
            margin-left: 30px;
            text-align: left;
            display: flex;
            align-items: center;
          }
        }
      }
    }

  }
  .getty-missions {
    padding-bottom: 60px;

    align-items: left;
    @media(max-width: $screen-xs-max) {
      align-items: center;
    }
    &.mission-list {
      .no-missions {
          .infographic {
            width: 215px;
        }
      }
    }
  }
  .mission-list {
    padding-left: 135px;
    padding-right: 135px;
    padding-top: 56px;
    @media (max-width: $screen-sm-max) {
      padding-left: 30px;
      padding-right: 30px;
    }
    @media (max-width: $screen-xs-max) {
      padding: 0;
      padding-bottom: 30px;
    }
    .no-missions {
      text-align: center;
      justify-content: center;
      display: flex;
      align-items: center;
      flex-direction: column;
      border-top: 2px solid #eee;
      width: 100%;
      @media (max-width: $screen-xs-max) {
        width: 80%;
        border: none;
        font-size: 15px;
      }

      .infographic {
        width: 175px;
        margin-top: 55px;
      }
      h2 {
        font-size: 25px;
        font-weight: 400!important;
        letter-spacing: normal;
        @media (max-width: $screen-xs-max) {
          line-height: 1.3;
          font-size: 18px;
        }

      }
      .turquoise-button {
        @media (max-width: $screen-xs-max) {
          margin-top: 30px;
          margin-bottom: 10px;
        }
        margin-top: 40px;
      }
    }
    .mission-header {
      width: 100%;
      box-shadow: none;
      padding: 0;
      background-color: transparent;
      @media (max-width: $screen-xs-max) {
        border-bottom: 1px solid #eee;
        padding-left: 20px;
        padding-top: 20px;
      }
      h2 {
        font-family: roboto;
        font-weight: 500!important;
        font-size: 20px;
        letter-spacing: 1.5px;
        @media (max-width: $screen-xs-max) {
          font-size: 18px;
        }
      }
    }
    .map-view-button {
      width: 130px;
      height: 36px;
      box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
      font-family: roboto;
      font-weight: 400;
      color: #8D95A2;
      font-size: 15px;
      justify-content: center;
      display: flex;
      align-items: center;
      border: 1px solid #E1E3E7;
      border-radius: 4px;
      cursor: pointer;
      letter-spacing: 1.5px;
      @media (max-width: $screen-xs-max) {
        margin-right: 10px;
      }

    }
    .mission-list-header {
      line-height: 30px;
      font-family: roboto;
      font-weight: lighter;
      color: #999999;
    }
    .mission-list-columns {
      display: flex;
      width: 100%;

      justify-content: space-between;
      .mission-location {
        width: 30%;
        min-width: 200px;
      }
      .mission-status {
        width: 16%;
        min-width: 100px;
      }
      .mission-flight-date {
        width: 16%;
        min-width: 100px;
        &.disabled   {
         background-color: white;
         color: $text-gray;
         opacity: 0.5;
        }
      }
      .mission-payout {
        width: 14%;
        min-width: 30px;
      }
      .mission-action {
        width: 25%;
        min-width: 260px;
      }
    }
    .mission-list-data {
      width: 100%;
    }
  }
}
.map-view-close {
  top: -25px !important;
  right: -25px !important;
  width: 50px;
  z-index: 500;
  height: 50px;
  background-color: white;
  border-radius: 25px;
  justify-content: center;
  display: flex;
  align-items: center;
  padding-bottom: 5px;
  padding-left: 2px;
  box-shadow: 0px 3px 6px rgba(0,0,0,0.3);
}
