.accept-mission {
  .title-bar {
    background-color: $brand-turqoise;
    padding: 10px;
    text-align: center;
  }
  .buttons {
    padding: 15px 15px 35px;
    background-color: $extra-light-gray;
    display: flex;
    .button {
      @include button;
      &:hover {
        border-color: $brand-orange;
        background-color: $brand-orange;
        color: white;
        text-decoration: none;
      }
      border-color: $text-darkblue;
      color: $text-darkblue;
      display: flex;
      justify-content: center;
    }
  }
}
