@import "dronebase-font-variables";

$dronebase-icon-font: 'DroneBase_icons_120716';

@font-face {
  font-family: 'DroneBase_icons_120716';
  src:  url('#{$icomoon-font-path}/DroneBase_icons_120716.eot?tb1h50');
  src:  url('#{$icomoon-font-path}/DroneBase_icons_120716.eot?tb1h50#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/DroneBase_icons_120716.ttf?tb1h50') format('truetype'),
    url('#{$icomoon-font-path}/DroneBase_icons_120716.woff?tb1h50') format('woff'),
    url('#{$icomoon-font-path}/DroneBase_icons_120716.svg?tb1h50#DroneBase_icons_120716') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'DroneBase_icons_120716' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

