.mission-header {
  padding: 15px 36px;
  display: flex;
  background-color: white;
  justify-content: space-between;
  z-index: 1;
  position: relative;
  @media (min-width: $screen-xs-max) {
    @include bottom-shadow;
  }
  @media (max-width: $screen-xs-max) {
    padding: 15px 10px;
    font-size: 12px;
  }
  table {
    width: 100%;
    line-height: normal;
    td {
      padding: 1.5px 0
    }
  }
  .title {
    @include headline-font;
    font-size: 30px;
    @media (max-width: $screen-xs-max) {
      font-size: 18px;
    }
    .id {
      color: $text-turqoise;
    }
  }
  .address {
    color: #888;
  }
  .payout {
    font-weight: bold;
    color: $brand-orange;
    @media (max-width: $screen-xs-max) {
      vertical-align: middle;
      font-size: 28px;
    }
  }
  .status {
    padding-top: 10px;
    font-size: 15px;
    .status-button {
      margin-top: 5px;
    }
  }
  .mission-header-icon-text {
    display: table-cell;
  }
  .mission-header-icon {
    display: table-cell;
    padding-right: 3px;
    padding-top: 1px;
  }
}
