#video-player-container {
  width: 100%;
  height: 100%;
  display: flex;

  cursor: pointer;

  .video-player {
    width: 100%;
    height: 100%;
  }
  .play-hide {
    display: flex;
    top: calc(50% - 75px);
    left: calc(50% - 75px);


    pointer-events: none;
    position: absolute;
    img {
      width: 150px;
      position: absolute;
      opacity: 0;
      transition: opacity .25s ease-in-out;
      -moz-transition: opacity .25s ease-in-out;
      -webkit-transition: opacity .25s ease-in-out;

    }
    @media (max-width: $screen-sm-max) {
      top: calc(50% - 50px);
      left: calc(50% - 50px);
      img {
        width: 100px;
      }
    }
  }
  .video-controls {
    transition: opacity .25s ease-in-out;
    -moz-transition: opacity .25s ease-in-out;
    -webkit-transition: opacity .25s ease-in-out;
    opacity: 0;
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.75);
    width: 120px;
    height: 50px;

    justify-content: space-around;
    -webkit-box-pack: space-around;
    -webkit-justify-content: space-around;
    -ms-flex-pack: space-around;
    flex: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    img#mute, img#volume-on {
      width: 44px;
    }
    img#volume-on {
      display: none;
    }
    .video-control-button {
      position: relative;
      margin-right: 10px;
      margin-left: 10px;
      width: 34px;
      padding: 5px;
      margin: 0;
      cursor: pointer;
    }
  }
  .no-audio {
    width: 50px;
  }
}
