.status-button {
  color: #888;
  float: left;
  &:last-child {
    padding-left: 15px;
  }
  &.showPointer {
    cursor: pointer;
  }
  .status-icon-text {
    display: table-cell;
    vertical-align: middle;
    font-size: 14px;
  }
  .status-icon-cell {
    display: table-cell;
    padding-right: 3px;
    padding-top: 1px;
    &.icon-Checkmark_b {
      color: $brand-turqoise;
      font-size: 18px;
      padding-right: 4px;
    }
    &.icon-Checkmark_c {
      color: $text-gray;
      font-size: 18px;
    }
  }
}
