.available-mission {
  border-top: 2px solid #eee;
  width: 100%;
  .map-container {
    padding: 0;
    #map-canvas {
      height: 500px;
      @media (max-width: $screen-xs-max) {
        height: 200px;
      }
    }
  }
  background: $extra-light-gray;
  height: 100%;
  position: absolute;
  top: 70px;
  @media (max-width: $screen-xs-max) {
    height: 200px;
    position: relative;
    top: 0;
  }

  .mission-content {
    background: $extra-light-gray;
    margin-top: 20px;
    @media (max-width: $screen-xs-max) {
      margin-top: 0px;
      background: white;
    }
    @media (min-width: $screen-sm-min) {
      padding: 0 50px;
    }
  }
  .count {
    font-size: 14px;
  }
  .instructions {
    display: flex;
    flex-direction: column;
    flex-grow: 2;
    margin-top: 38px;
    width: 100%;
    justify-content: space-between;
    align-content: stretch;
    padding-right: 20px;
    margin-bottom: 150px;
    @media (max-width: $screen-xs-max) {
      margin-top: 2px;
      flex-direction: column;
      max-height: none;
      padding-right: 0;
      margin-bottom: 20px;
    }
    h3 {
      font-size: 22px;
      color: $text-darkblue;
      @media (max-width: $screen-xs-max) {
        margin-left: 20px;
      }
    }
  }

  .training-mission {
    display: flex;
    padding: 20px !important;
    margin-top: 20px;
    @media (max-width: $screen-xs-max) {
      border-bottom: 3px solid #eee;
      padding-bottom: 30px!important;
      padding-top: 30px!important;
      margin-top: -2px!important;
    }
    img {
      margin-right: 20px;
      width: 24px;
      height: 33px;
    }
    color: #1D2D47;
    .training-mission-label {
      font-size: 16px;
      font-weight: 500;
    }
  }

  .laanc-exception {
    margin-top: 15px;
    padding: 10px;
    border: 1px solid #3FC0C2;
    border-radius: 11px;
    color: #3FC0C2;
    font-size: 14px;
    .authorization {
      font-size: 18px;
      font-weight: bold;
    }
    a {
      color: #3fc0c2;
      font-weight: bold;
      text-decoration: underline;
    }

  }
  .instructions-panel {
    padding: 1px 20px;
    background-color: white;
    flex-grow: 2;
    overflow-y: scroll;
    max-height: 600px;
    margin-bottom: 15px;
    font-size: 14px;
    line-height: 20px;
    @media (max-width: $screen-xs-max) {
      margin-right: 0;
    }
    .shots-panel {
    }
    .shots-panel-intructions {
      margin-left: 10px;
    }
    h3 {
      color: $brand-orange;
      font-size: 18px;
      @media (max-width: $screen-xs-max) {
        margin-left: 0;
      }
    }
    .turquoise-border {
      margin-left: -10px;
      margin-right: -10px;
      margin-top: 10px;
      margin-bottom: 10px;
      padding: 10px;
      padding-top: 16px;
      padding-bottom: 20px;
      border: 2px solid rgba(70,214,216, .5);
      border-radius: 5px;
      font-size: 14px;
      @media (max-width: $screen-xs-max) {
        padding-left: 20px;
        padding-top: 12px;
        padding-bottom: 20px;
        margin-top: 20px;
        margin-left: -5px;
        margin-right: -5px;
      }
      .turquoise-header {
        color: #149B9E;
        font-size: 18px;
        margin-left: 10px;
        @media (max-width: $screen-xs-max) {
          margin-left: 0;
        }
      }
      .flight-app-instruction {
        display: flex;
        margin-top: 15px;
        font-size: 14px;
        color: #1D2D47;
        margin-left: 10px;
        align-items: center;
        @media (max-width: $screen-xs-max) {
          margin-left: 0px;
          display: block;
        }
        p {
          margin-bottom: 0;
        }
        .flight-app-header-and-caret {
          display: flex;
          align-items: center;
          min-width: fit-content;
        }
        .flight-app-caret {
          height: 10px;
          width: 10px;
          margin-right: 15px;
          @media (max-width: $screen-xs-max) {
            margin-right: 10px;
          }
        }
        .flight-app-header {
          font-weight: 500;
          margin-right: 5px;
          min-width: fit-content;
        }
        .flight-app-instructions {
          @media (max-width: $screen-xs-max) {
            margin-left: 20px;
          }
        }
      }
    }
  }
  .additional-info {
    background-color: white;
    padding: 12px 20px;
  }
  .details-box-row {
    display:  flex;
    flex-direction: row;
    @media (max-width: $screen-xs-max) {
      flex-direction: column;
    }
    justify-content: space-between;
    flex-grow: 2;
    .details-box {
      width: 50%;
      height: 75px;
      @media (max-width: $screen-xs-max) {
        width: 100%;
        height: 65px;
        border-top: 3px solid $extra-light-gray;
        border-bottom: 3px solid $extra-light-gray;
        &:last-child {
          border-top: none;
        }
        &:first-child {
          margin-bottom: 0;
        }
      }
      background: white;
      padding: 5px;
      &:first-child {
        margin-right: 20px;
        @media (max-width: $screen-xs-max) {
          margin-right: none;
        }
      }
      margin-bottom: 15px;
      @media (max-width: $screen-xs-max) {
        margin-bottom: 2px;
      }
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      .details-box-label {
        display: flex;
        justify-content: center;
        width: 200px;
        font-size: 18px;
        .icon-Payment {
          margin-right: 8px;
          font-size: 24px;
        }
        .icon-Calendar {
          margin-right: 8px;
          font-size: 24px;
        }
      }
      .details-box-content {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        font-family: $text-font;
        font-size: 18px;
        background-color: white;
        color: $brand-darkblue;
        width: 100%;
        flex-grow: 2;
        border-left: 3px solid $extra-light-gray;
        height: 58px;
        @media (max-width: $screen-xs-max) {
          height: 50px;
        }
        .details-box-content-above {
          width: 100%;
          text-align: center;
          color: $brand-orange;
          font-size: 20px;
        }
        .details-box-content-below {
          width: 100%;
          text-align: center;
          font-size: 12px;
          color: $text-gray;
        }
      }
    }
  }

  .buttons {
    padding: 15px 15px 35px;
    background-color: $extra-light-gray;
    display: flex;
    .button {
      @include button;
      &:hover {
        border-color: $brand-orange;
        background-color: $brand-orange;
        color: white;
        text-decoration: none;
      }
      border-color: $text-darkblue;
      color: $text-darkblue;
      display: flex;
      justify-content: center;
    }
  }
  .orange-button {
    color: white;
    background-color: $brand-orange;
    border: none;
    padding: 5px 20px;
  }
}
#mobileAcceptDenyFixedButton {
  display: none !important;
  @media (max-width: $screen-xs-max) {
    display: block;
    cursor: pointer;
    position: fixed;
    bottom: 0;
    width: 100%;
    text-align: center;
    background-color: $brand-turqoise;
    padding: 15px;
    color: white;
    font-weight: bold;
    font-size: 22px;
  }
}
