#publicsearch {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
  background: white;
  display: flex;
  color: $text-darkblue;
  @media (max-width: $screen-xs-max) {
    flex-direction: column;
  }
  .title {
    @include headline-font;
    font-size: 52px;
    line-height: 1;
    @media (max-width: $screen-xs-max) {
      font-size: 34px;
    }
  }
  .smalltext {
    margin: 18px 0;
    font-size: 18px;
    @media (max-width: $screen-xs-max) {
      font-size: 15px;
    }
  }
  .text-section {
    width: calc(33% - 15px);
    background-color: $extra-light-gray;
    padding: 5vw;
    height: 100%;
    @media (max-width: $screen-xs-max) {
      width: 100%;
      padding: 50px 20px;
      position: absolute;
    }
  }
  .map-canvas-publicsearch {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .map-container-dashboard {
    position: absolute;
    top: 0 !important;
    bottom: 0;
    left: 33% !important;
    right: 0;
    @media (max-width: $screen-xs-max) {
      left: 0% !important;
      top: 335px !important;
    }
  }
  .divider {
    width: 15px;
    height: 100%;
    background-color: white;
  }
  .button {
    @include button;
    width: 150px;
    &:hover {
      border-color: $brand-orange;
      background-color: $brand-orange;
      color: white;
      text-decoration: none;
    }
    border-color: $brand-orange;
    color: $brand-orange;
    display: flex;
    justify-content: center;
  }
  .find-button {
    width: 34px;
    height: 34px;
    background-color: $brand-turqoise;
    color: white;
    font-size: 24px;
    cursor: pointer;
    padding-left: 7px;
    padding-top: 7px;
  }
  input {
    height: 34px;
    border: 1px solid #ccc;
    font-size: 16px;
    width: 240px;
    padding-left: 12px;
  }
  .inputwrapper {
    display: flex;
    flex-direction: row;
  }
}
#publicsearch-pano-infowindow {
  height: 80px;
  display: flex;
  img {
    height: 80px;
  }
  .content {
    position: relative;
    margin: 15px 15px 15px 20px;
  }
  .icon {
    position: absolute;
    left: -15px;
    top: 2px;
    color: #aaa;
  }
  .link {
    color: $brand-turqoise;
  }
}
