.client-mission .mission-details .mission-details-box {
  &.reschedule {
    padding: 20px;
    padding-right: 25px;
    text-align: left;
    position: relative;
    display: block;
    height: auto;
    .close-box {
      position: absolute;
      top: 10px;
      right: 10px;
      color: gray;
    }
    .mission-details-reschedule-title {
      color: $brand-orange;
      font-size: 18px;
    }
    .mission-details-reschedule-description {
      color: #777777;
      font-size: 14px;
      margin: 15px 0;
    }
    .mission-details-reschedule-reason {
      .nice-select {
        margin-top: 5px;
        border-radius: 0;
        border: 1px solid #ccc;
        font-weight: 100;
        border-radius: 3px;
        padding: 0 30px 0 5px;
        width: 100%;
        float: none;
        color: #1D2D47;
      }
      .nice-select:after {
        height: 10px;
        width: 10px;
        margin-top: -6px;
      }
      .nice-select .list {
        display: none;
      }
      .nice-select.open .list {
        border-radius: 0;
        margin-top: 1px;
        display: block;
        color: black;
        max-height: 350px;
        overflow-y: scroll;
        width: 100%;
        .option {
          font-weight: 100;
          &.selected {
            font-weight: 400;
          }
        }
      }
      margin-bottom: 20px;
    }
    .mission-details-reschedule-notes {
      .notes-label {
        font-size: 14px;
        margin-bottom: 8px;
      }
      margin: 20px 0;
      color: #1D2D47;
      textarea {
        border-radius: 3px;
        color: #1D2D47;
        font-size: 14px;
        padding: 15px;
        &::placeholder {
          color: #8D95A2;
        }
      }
    }
    .mission-details-reschedule-calendar {
      .schedule-mission-datetime-button:hover, .schedule-mission-datetime-button:focus {
        background-color: #3FC0C2;
        border-color: #3FC0C2;
        color: white;
      }
      .bootstrap-datetimepicker-widget table {
        color: #777777;
        td.active, td.active:hover {
          background-color: #3FC0C2;
          border-radius: 20px;
        }
        .day.disabled {
          color: #f4f4f4;
        }
      }
      .bootstrap-datetimepicker-widget ~ .schedule-mission-datetime-button {
        background-color: #3FC0C2;
        color: white;
      }
      .date-entered {
        input {
          border-color: #3FC0C2;
        }
        .schedule-mission-datetime-button {
          border-color: #3FC0C2;
          background-color: #3FC0C2;
          color: white;
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
        }
      }
    }
    .mission-detail-reschedule-laanc-note {
      font-size: 12px;
      color: #777777;
      line-height: 15px;
      max-width: 240px;
      margin-bottom: 16px;
    }
    .schedule-mission-button {
      padding: 10px 0px;
      width: 133px;
      margin-bottom: 10px;
      color: white;
      background-color: $brand-turqoise;
      border: none;
      border-radius: 2px;
      text-transform: uppercase;
      font-size: 14px;
      box-shadow: rgba(0, 0, 0, 0.16) 0px 2px 1px;
      letter-spacing: 1.5px;
      &:hover {
        background-color: #149B9E;
        text-decoration: none;
      }
      &.disabled, &.disabled:hover {
        background-color: #C5C9D0;
        pointer-events: none;
      }
    }
  }
}
.ember-modal-overlay .ember-modal-dialog .ember-modal-inner-wrap.reschedule-success-modal {
  text-align: center;
  padding: 40px 100px;
  position: relative;
  .modal-close-container {
    position: absolute;
    top: 15px;
    right: 15px;
    a {
      color: #a1a1a1;
      cursor: pointer;
      &:hover {
        text-decoration: none;
      }
    }
  }
  .header {
    color: $brand-orange;
    line-height: 34px;
    font-size: 30px;
    text-transform: uppercase;
    font-family: dense;
    letter-spacing: 2px;
  }
  .body {
    margin-top: 20px;
    font-size: 12px;
    line-height: 20px;
    color: $brand-darkblue;
    .time div {
      display: inline-block;
    }
  }
  .button {
    padding: 10px 0px;
    width: 133px;
    margin-bottom: 10px;
    margin-top: 25px;
    color: white;
    background-color: $brand-turqoise;
    border: none;
    border-radius: 2px;
    text-transform: uppercase;
    font-size: 14px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 2px 1px;
    &:hover {
      background-color: #149B9E;
      text-decoration: none;
    }
  }
}
.reschedule-icon {
  background: url("/assets/images/reschedule_icon.png") no-repeat center center;
  width: 160px;
  height: 160px;
}
.modal-success-image {
  margin: auto;
}

.client-mission .mission-details .mission-details-box.date-scheduled {
  height: 92px;
  .mission-details-box-content {
    color: $brand-darkblue;
    font-size: 18px;
  }
}
