.pilot-details {
  background-color: $brand-darkblue;
  height: 350px;
  color: $brand-mint;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  @include headline-font;
  font-size: 19px;
  justify-content: space-around;
  margin: 0;
  padding: 0 20px;
  align-items: stretch;
  @media (max-width: $screen-xs-max) {
    height: 275px;
  }
  .pilot-img {
    width: 90px;
    margin: auto;
    padding: 22px 0 10px;
    @media (max-width: $screen-xs-max) {
      width: 45px;
      padding-top: 15px;
    }
  }
  .icon-Drone_a {
    margin: -15px 0 -10px 0;
  }
  .name {
    align-self: center;
    font-family: $text-font;
    font-weight: lighter;
    font-size: 18px;
    margin-bottom: 22px;
    @media (max-width: $screen-xs-max) {
      margin-bottom: 15px;
    }
  }
  .left-box,.right-box,.full-box {
    flex-grow: 1;
    padding: 2px;
  }
  .full-box {
    border-bottom: 1px solid $brand-orange;
  }
  .left-box {
    border-right: 1px solid $brand-orange;
  }
  .top-box {
    border-top: 1px solid $brand-orange;
  }
  .right-box {
    padding-left: 10px;
  }
  .split {
    display: flex;
    flex-grow: 1;
    justify-content: flex-start;
    align-content: flex-start;
    border-bottom: 1px solid $brand-orange;
  }
  .button {
    @include button;
    &:hover {
      background-color: $brand-mint;
      color: $text-darkblue;
      text-decoration: none;
    }
    align-self: center;
    border: 1px solid $brand-mint;
    color: $brand-mint;
    margin-bottom: 22px;
    margin-top: 22px;
    @media (max-width: $screen-xs-max) {
      margin-top: 15px;
      margin-bottom: 15px;
    }
  }
  .small-text {
    color: $brand-orange;
    font-size: 10px;
    font-family: $text-font;
    padding-right: 10px;
    vertical-align: middle;
  }
}
