.explore-card {
  background-image: url("/assets/images/pilot/explore_background_image.jpg");
  padding: 70px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #1F2B45;
  @media (max-width: $screen-xs-max) {
    background-image: none;
    padding: 40px 0px;
  }
  h1 {
    margin-top: 0;
    color: white;
    font-family: Dense;

    font-size: 60px;
    letter-spacing: 1.5px;

    @media (max-width: $screen-xs-max) {
      color: #1F2B45;
      font-size: 50px;
    }

  }
  .blog-posts {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 50px;
    margin-bottom: 170px;
    max-width: 1230px;
    @media (max-width: $screen-xs-max) {
      display: block;
      justify-content: center;
      width: 90%;
      margin-bottom: 20px;
      margin-top: 30px;
    }

    .blog-post {
      margin-left: 15px;
      margin-right: 15px;
      border-radius: 10px;
      width: 370px;
      box-shadow: 0px 10px 20px rgba(0,0,0,0.35);
      margin-bottom: 30px;
      @media (max-width: $screen-xs-max) {
        width: 100%;
        box-shadow: 0px 5px 15px rgba(0,0,0,0.35);
        margin-bottom: 30px;
        margin-right: 0;
        margin-left: 0;
      }
    }
  }
  .card-header {
    font-size: 24px;
    padding-bottom: 20px;
    font-weight: 400;
    @media (max-width: $screen-xs-max) {
      font-size: 22px;
    }
  }
  .card-text {
    font-size: 16px;
    color: #777777;
  }

  .card-image {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    width: 370px;
    @media (max-width: $screen-xs-max) {
      width: 100%;
    }

  }
  .card-body {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background: white;
    padding: 30px 30px;
    height: 260px;
    @media (max-width: $screen-xs-max) {
      padding: 25px 30px;
      height: auto;
    }
    .less-margin {
      margin-top: 15px;
      @media (max-width: $screen-xs-max) {
        margin-top: 35px;
      }
    }
  }

  .main-card {
    margin-top: 70px;
    display: flex;
    border-radius: 10px;
    box-shadow: 0px 19px 38px rgba(0,0,0,0.16);
    max-width: 1170px;
    background: transparent;
    @media (max-width: $screen-xs-max) {
      display: block;
      width: 100%;
      margin-top: 30px;
      box-shadow: none;
      border-radius: 0;
    }

    .main-card-image {
      border-top-left-radius: 10px;
      border-bottom-left-radius: 10px;
      width: 45%;
      object-fit: cover;
      @media (max-width: $screen-xs-max) {
        border: none;
        width: 100%;
        border-radius: 0;
        border-top-left-radius: 0px;
        border-bottom-left-radius: 0px;
      }
    }
    .main-card-body {
      border-top-right-radius: 10px;
      border-bottom-right-radius: 10px;
      background: white;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 60px;
      @media (max-width: $screen-xs-max) {
        width: 100%;
        border-radius: 0;
        padding: 30px;
      }

    }
    .download-buttons {
      margin-top: 30px;
      @media (max-width: $screen-xs-max) {
        margin-top: 5px;
      }
    }
    .app-store-badge {
      margin: 10px;
      width: 195px;
      @media (max-width: $screen-xs-max) {
        margin-top: 20px;
        width: 145px;
      }
    }
  }
}
